<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='ใบแจ้งหนี้')==-1"></NoAccess><div class="container-fluid p-4" v-else><StepBilling step="2"></StepBilling><h2 class="text-decoration-underline mb-4">รายการใบแจ้งหนี้</h2><div class="modal fade" id="editInvoiceModal"><div class="modal-dialog modal-xs"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="exampleModalLabel">แก้ไขข้อมูลปริมาณการใช้น้ำ</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><form><div class="mb-3"><label class="form-label">ปริมาณการใช้น้ำ (เดิม)</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model="doc.flatRate"><div class="input-group-append"><span class="input-group-text" id="basic-addon2">ลบ.ม.    </span></div></div></div><div class="mb-3"><label class="form-label">ปริมาณการใช้น้ำ (แก้ไข)</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model="doc.flatRate"><div class="input-group-append"><span class="input-group-text" id="basic-addon2">ลบ.ม.</span></div></div></div><div class="mb-3"><div class="form-group"><textarea class="form-control" id="exampleFormControlTextarea1" rows="/" placeholder="หมายเหตุ">     </textarea></div></div><div class="mb-3"><div class="row"><label class="form-label">แนบใบคำร้อง</label><JMUploads></JMUploads></div></div><div class="modal-footer justify-content-center"><div class="d-flex"><button class="btn btn-secondary mx-2" type="button" @click="$router.go(-1)">ยกเลิก</button><button class="btn btn-success mx-2" type="button" @click="submit()">ยืนยัน</button></div></div></form></div></div></div></div><JMBillingsearch @search="fetchData()" @clear="docs = []; totalCount=0; totalQty=0; totalAmount=0; totalDebt=0;" v-model="search" isClear></JMBillingsearch><div class="row"><label class="form-label">ค้นหายอดที่ต้องชำระ</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="search.unPaid"></div></div><div class="col-6"> <div class="row mb-2"> <label>สถานะสั่งพิมพ์</label></div><div class="row mb-3"> <div class="col-3"><div class="form-check"><input class="form-check-input" id="print1" type="radio" name="print" :value="true" v-model="filter.isPrint"><label class="form-check-label" for="print1">สั่งพิมพ์แล้ว</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="fprint2" type="radio" name="print" :value="false" v-model="filter.isPrint"><label class="form-check-label" for="fprint2"> ยังไม่ได้สั่งพิมพ์</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="fprint3" type="radio" name="print" :value="undefined" v-model="filter.isPrint"><label class="form-check-label" for="fprint3"> ทั้งหมด</label></div></div></div></div><div class="row"><img :src="doc.signManager1" width="200"><div class="col-2 d-flex justify-content-start"><div class="row"><label>จำนวนที่แสดง   </label><select class="form-select form-select-sm mb-2 mx-2" v-model="show" @change="fetchData(),clearPage()"><option selected="" :value="999999999">ทั้งหมด</option><option :value="50">50</option></select></div></div><div class="col-2 d-flex justify-content-start"><div class="row"><label>เรียงตาม   </label><select class="form-select form-select-sm mb-2 mx-2" v-model="sort" @change="fetchData()"><option value="{&quot;no&quot;:1}">ลำดับในไฟล์ Excel</option><option value="{&quot;createdAt&quot;:-1}">ตามวันที่</option><option value="{&quot;meter&quot;:1}">เลขที่ผู้ใช้บริการ น้อยไปมาก</option><option value="{&quot;meter&quot;:-1}">เลขที่ผู้ใช้บริการ มากไปน้อย</option><option value="{&quot;sequence&quot;:1}">เลขที่ใบแจ้งหนี้ น้อยไปมาก</option><option value="{&quot;sequence&quot;:-1}">เลขที่ใบแจ้งหนี้ มากไปน้อย</option><option value="{&quot;name&quot;:-1}">ชื่อ ก-ฮ</option><option value="{&quot;name&quot;:1}">ชื่อ ฮ-ก</option><option value="{&quot;address&quot;:-1}">ที่อยู่ ก-ฮ</option><option value="{&quot;address&quot;:1}">ที่อยู่ ฮ-ก</option></select></div></div><div class="col d-flex justify-content-end mb-2"><button class="btn btn-danger text-white mx-3" v-if="selected.length != 0" type="button" @click="deleteMany()">ลบรายการ </button><div class="row"><button class="btn btn-outline-success text-dark" type="button" :disabled="(selected.length == 0)" @click="downloadExcel()"> <span>ดาวน์โหลด Excel ({{selected.length}}) รายการ</span></button><button class="btn btn-primary text-white" type="button" :disabled="(selected.length == 0)" @click="btnClick()">พิมพ์ใบแจ้งหนี้ ({{selected.length}}) ใบ<i class="fas fa-print ms-2"></i></button></div></div></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span>กำลังดึงข้อมูลจากแม่ข่าย</span></div></transition></div></div><div class="row mt-2"><div class="col"><span>จำนวนผู้ใช้น้ำ </span><span class="text-primary" id="totalCount">{{(totalCount??0).formatComma()}} </span><span> คน</span><span class="ms-3"> |</span><span class="ms-4">ปริมาณการใช้น้ำ</span><span class="text-primary mx-2">{{(totalQty??0).formatFull()}}</span><span>ลบ.ม.</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินที่ต้องเรียกเก็บ </span><span class="text-primary mx-2">{{docs.map(el=>((el.totalAmount??0)+(el.vat??0))).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินที่ต้องเรียกเก็บ + ยอดค้างชำระ</span><span class="text-primary mx-2">{{docs.map(el=>((el.invoiceAmount??0))).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="mx-3">|</span><span>ข้อมูลอัปเดตล่าสุด: </span><span class="text-primary">{{getThaiMonth(latest.month)}} {{(latest.year??0).toString().substring(2, 4)}}</span></div><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"><th scope="col"> <input type="checkbox" @change="selectAll($event)"></th><th scope="col">ลำดับ</th><th scope="col">เลขที่ใบแจ้งหนี้</th><th scope="col">เลขที่ผู้ใช้น้ำ</th><th scope="col">ประเภท</th><th scope="col">ประจำเดือน</th><th scope="col">ชื่อ</th><th scope="col">ที่อยู่</th><th class="text-end" scope="col">ยอดค้างชำระ</th><th class="text-end" scope="col">ปริมาณการใช้น้ำ (ลบ.ม.)</th><th class="text-end" scope="col">อัตราค่าบริการ (บาท)</th><th scope="col">สถานะ</th><th class="text-end" scope="col">รวมเงิน (บาท)</th><th class="text-end" scope="col">ภาษี (บาท)</th><th class="text-end" scope="col">ค่าบริการ (บาท)</th><th class="text-end" scope="col">ชำระทั้งสิ้น (บาท)</th></tr></thead><tbody><template v-for="item,i in docs"><tr class="text-center"><th><input type="checkbox" v-model="selected" :value="item._id"><i class="fas fa-print text-success" v-if="item.isPrint" data-toggle="tooltip" data-placement="top" title="พิมพ์ใบแจ้งหนี้เรียบร้อยแล้ว!"></i></th><th scope="row">{{(page*show)+(i+1)}}</th><td class="text-start"><a class="card-link" :href="`/portal/invoice/form/${item._id}?searchparam=${JSON.stringify(search)}`">{{item.sequence}}</a></td><td class="text-start"><a class="card-link" :href="`/portal/meter/history/${item.meter}`">{{item.meter}}</a></td><td class="text-center">{{item.category}}</td><td class="text-center"> <template v-if="item.month!=undefined&amp;&amp;item.year!=undefined"><span>{{getThaiMonth(item.month)}}{{(item.year).toString().substring(2, 4)}}</span></template></td><td class="text-start">{{item.name}}</td><td>{{item.address}}</td><td class="text-end"> <span>{{(item.debtAmount??0).formatFull()}}</span></td><td class="text-end">{{(item.qty??0).formatFull()}}</td><td class="text-end">{{(item.rate??0).formatFull()}}</td><td>{{item.categoryType}}</td><td class="text-end">{{(item.totalAmount??0).formatFull()}}</td><td class="text-end">{{(item.vat??0).formatFull()}}</td><td class="text-end">{{((item.totalAmount??0)+(item.vat??0)).formatFull()}}</td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}} </td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>