<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='ใบเสร็จรับเงิน/กำกับภาษี')==-1"></NoAccess><div v-else><div class="modal fade" id="signModal" tabindex="0" aria-labelledby="signModalLabel" aria-hidden="true"><div class="modal-dialog"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="signModalLabel">ลงลายมือชื่อให้ความเห็น</h5></div><div class="modal-body"><label class="form-label">ลงลายมือชื่อยืนยัน</label><JMSign v-model="signature"></JMSign></div><div class="modal-footer"><button class="btn btn-light btn btn-secondary" type="button" data-bs-dismiss="modal">ยกเลิก</button><button class="btn btn-success text-white btn btn-secondary" type="button" data-bs-dismiss="modal" @click="addSignature()">อนุมัติ</button></div></div></div></div><div class="container-fluid p-4"><StepBilling step="4"></StepBilling><h2 class="text-decoration-underline mb-4">รายการใบเสร็จรับเงิน/ใบกำกับภาษี</h2><JMBillingsearch @search="fetchData()" @clear="docs = []" v-model="search" isClear></JMBillingsearch><div class="row"><div class="col-5"> <div class="row mb-2"> <label>ลงนาม</label></div><div class="row mb-3"> <div class="col-3"><div class="form-check"><input class="form-check-input" id="tsign" type="radio" name="sign" :value="true" v-model="filter.isSign"><label class="form-check-label" for="tsign">ลงนาม</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="fsign" type="radio" name="sign" :value="false" v-model="filter.isSign"><label class="form-check-label" for="fsign">ยังไม่ได้ลงนาม</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="usign" type="radio" name="sign" :value="undefined" v-model="filter.isSign"><label class="form-check-label" for="usign"> ทั้งหมด</label></div></div></div></div><div class="col-5"> <div class="row mb-2"> <label>สถานะสั่งพิมพ์</label></div><div class="row mb-3"> <div class="col-3"><div class="form-check"><input class="form-check-input" id="print1" type="radio" name="print" :value="true" v-model="filter.isPrint"><label class="form-check-label" for="print1">สั่งพิมพ์แล้ว</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="fprint2" type="radio" name="print" :value="false" v-model="filter.isPrint"><label class="form-check-label" for="fprint2"> ยังไม่ได้สั่งพิมพ์</label></div></div><div class="col-3"><div class="form-check"><input class="form-check-input" id="fprint3" type="radio" name="print" :value="undefined" v-model="filter.isPrint"><label class="form-check-label" for="fprint3"> ทั้งหมด</label></div></div></div></div></div><div class="row mt-3"><div class="col-2 d-flex justify-content-start"><div class="row"><label>จำนวนที่แสดง</label><select class="form-select form-select-sm mb-2" aria-label=".form-select-lg example" v-model="show" @change="fetchData(),clearPage()"> <option selected="" :value="999999999">ทั้งหมด</option><option :value="50">50</option></select></div></div><div class="col-2 d-flex justify-content-start"><div class="row"><label>เรียงตาม   </label><select class="form-select form-select-sm mb-2 mx-2" v-model="sort" @change="fetchData()"><option value="{&quot;createdAt&quot;:-1}">ตามวันที่</option><option value="{&quot;meter&quot;:1}">เลขที่ผู้ใช้บริการ น้อยไปมาก</option><option value="{&quot;meter&quot;:-1}">เลขที่ผู้ใช้บริการ มากไปน้อย</option><option value="{&quot;sequence&quot;:1}">เลขที่ใบเสร็จ น้อยไปมาก</option><option value="{&quot;sequence&quot;:-1}">เลขที่ใบเสร็จ มากไปน้อย</option><option value="{&quot;name&quot;:-1}">ชื่อ ก-ฮ</option><option value="{&quot;name&quot;:1}">ชื่อ ฮ-ก</option><option value="{&quot;address&quot;:-1}">ที่อยู่ ก-ฮ</option><option value="{&quot;address&quot;:1}">ที่อยู่ ฮ-ก</option></select></div></div><div class="col d-flex justify-content-end align-items-center mb-2"><button class="btn btn-danger text-white mx-3" v-if="selected.length != 0" type="button" @click="deleteMany()">ลบรายการ </button><div class="row"><button class="btn btn-outline-success text-dark" type="button" :disabled="(selected.length == 0)" @click="downloadExcel()"> <span>ดาวน์โหลด Excel ({{selected.length}}) รายการ</span></button><button class="btn btn-warning text-white" type="button" @click="signatureRequest()" v-if="filter.isSign==false"> <span>ส่งคำขอลงนาม</span></button><button class="btn btn-primary text-white" type="button" :disabled="(selected.length == 0)" @click="btnClick()" v-if="filter.isSign==true">พิมพ์ใบเสร็จ ({{selected.length}}) ใบ<i class="fas fa-print"></i></button><button class="btn btn-primary text-white" type="button" :disabled="(selected.length == 0)" @click="btnClick()" v-if="filter.isSign!=true">แสดงร่าง ({{selected.length}}) ใบ<i class="fas fa-print"></i></button></div></div></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span>กำลังดึงข้อมูลจากแม่ข่าย</span></div></transition></div></div><div class="row mt-2"><div class="col"><span>จำนวนผู้ใช้น้ำ </span><span class="text-primary mx-2">{{(totalCount??0).formatComma()}} </span><span>คน</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินที่รับจริง</span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินตามใบแจ้งหนี้ </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">รวมภาษีขาย </span><span class="text-primary mx-2">{{docs.map(el=>el.vat).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">ยอดเงินคงค้าง</span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span>บาท</span><span class="mx-3">|</span><span>ข้อมูลอัปเดตล่าสุด: </span><span class="text-primary">{{getThaiMonth(latest.month)}} {{(latest.year??0).toString().substring(2, 4)}}</span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center"><th scope="col"> <input type="checkbox" @change="selectAll($event)"></th><th>ลำดับ</th><th>เลขที่ผู้ใช้น้ำ</th><th>ชื่อ</th><th>เลขที่ใบเสร็จ</th><th>ประเภท</th><th>ประจำเดือน</th><th class="text-end">จำนวนเงินตามใบแจ้งหนี้ (บาท)</th><th class="text-end">จำนวนเงินที่รับจริง (บาท)</th><th class="text-end">ยอดเงินคงค้าง (บาท)</th><th> <i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody><template v-for="item,i in docs"><tr><th class="text-center"><div class="row"><input type="checkbox" v-model="selected" :value="item._id"><i class="fas fa-receipt text-success mt-2" v-if="item.isPrint" data-toggle="tooltip" data-placement="top" title="พิมพ์ใบเสร็จเรียบร้อยแล้ว!"></i><i class="fas fa-check text-success mt-2" v-if="item.isApproved" data-toggle="tooltip" data-placement="top" title="ใบเสร็จอนุมัติแล้ว!"></i><i class="fad fa-file-signature text-dark mt-2" v-if="item.signature!=undefined" data-toggle="tooltip" data-placement="top" title="ใบเสร็จเซ็นอนุมัติแล้ว!"></i><img :src="item.signature" style="height:50px;width:auto;"></div></th><th class="text-center" scope="row">{{(page*show)+(i+1)}}</th><td class="text-start"><a class="card-link" :href="`/portal/meter/history/${item.meter}`">{{item.meter}}</a></td><td class="text-start">{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-start"><a class="card-link" :href="`/portal/receipt/form/${item._id}?searchparam=${JSON.stringify(search)}`">{{item.sequence}}</a></td><td class="text-center">{{item.category}}</td><td class="text-center"> <span>{{toThaiFormat(item.year,item.month)}}</span></td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}} </td><td class="text-end">{{(item.paymentAmount??0).formatFull()}}</td><td class="text-end">{{(item.debtAmount??0).formatFull()}}</td><td class="text-center"><template v-if="item.isSigned == true||item.signature!=undefined"><span>เซ็นแล้ว</span></template><template v-else-if="item.isApproved == true"><span>อนุมัติแล้ว</span></template><template v-else-if="item.isRequested == true"><span>ขออนุมัติ</span></template></td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div></div>