  toThaiFormat(dt: string) {
    var dtThai = DateTime.fromISO(dt)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th")
      .toFormat("ddLLLyy");
    return dtThai;
  }
  calcTreatment(doc: any): number {
    if (doc.category == 1) return doc.qty * 3.0;
    else if (doc.category == 2) return doc.qty * 3.5;
    else if (doc.category == 3) return doc.qty * 4.0;
    else return 0;
  }
  calcTax(doc: any): number {
    let basisAmount = doc.rate * doc.qty * 0.07;
    return basisAmount || 0;
  }
  sum(item: any): number {
    let sumAmount = 0;
    if (item.calculationType == "บาท/ลบ.ม.") {
      sumAmount = item.qty * item.rate;
    } else {
      sumAmount = item.rate;
    }
    // console.log(item.qty,item.rate,sumAmount,item.calculationType)
    return sumAmount ?? 0;
  }
