<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">นำเข้ารายการรหัสบัญชี</h2><div class="mb-3"><div class="input-group"><input class="form-control" type="file" label="Upload" @input="importExcel($event)"><button class="btn btn-outline-secondary" type="button">Upload</button></div></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" to="/portal/code/list">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="postData()">บันทึก</button></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center"><th class="align-middle">ลำดับ</th><th class="align-middle">รหัสบัญชี</th><th class="align-middle">ชื่อบัญชี</th><th class="align-middle">หมวดค่าใช้จ่าย</th></tr></thead><tbody><tr class="text-center" v-for="(item,i) in docs"><th scope="row">{{i+1}}</th><td>{{item.code}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.category}}</td></tr></tbody></table></div></div></div>