<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='dashboard billing')==-1"></NoAccess><div class="container-fluid px-5 row" v-else><div class="d-flex justify-content-end my-3 gap-2"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div></div><div class="container-fluid px-5 row d-print-none"><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-outline-success text-dark me-3" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Dashboard เป็น Excel</span></button><button class="btn btn-primary text-white" @click="generatePDF('print')">พิมพ์ PDF</button></div><div class="row d-print-none"><div class="col-2"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" v-model.number="search.fromYear"> <option v-for="item,i in 10">{{new Date().getFullYear()+543-i}}</option></select></div><div class="col-1 mt-5 text-center"><span>ถึง</span></div><div class="col-2"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" v-model.number="search.toYear"> <option v-for="item,i in 10">{{new Date().getFullYear()+543-i}}</option></select></div><div class="col-1"></div><div class="col-2"><label class="mt-3">เดือน</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.fromMonth"> <option selected="">กรุณาเลือก</option><option v-for="item in 12" :value="item">{{item}}</option></select></div><div class="col-1 mt-5 text-center"><span>ถึง</span></div><div class="col-2"><label class="mt-3">เดือน</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.toMonth"> <option selected="">กรุณาเลือก</option><option v-for="item in 12" :value="item">{{item}}</option></select></div><div class="col-6"><label class="mt-3">พื้นที่</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"><option :value="rates.map(el=&gt;el.code)">ทั้งหมด</option><option v-for="item in rates" :value="[item.code]">{{item.name}} ({{item.code}})</option></select></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="fetchData()">ค้นหา</button></div></div><div class="card dashboard-card mx-3 mt-5" style="width: 25rem;"><div class="card-body row"><div class="col"><div class="card-icon position-relative g2"><i class="fab fa-bitcoin text-white"></i></div></div><div class="col"><h5 class="card-title">เฉลี่ยยอดชำระ</h5><p class="card-text">{{(doc.paid12mo.map(el=>(el.total??0)).reduce((a,b)=>a+b,0)/12).formatFull()}} บาท/เดือน</p></div><hr class="mt-3"><p class="card-text">***</p></div></div><div class="card dashboard-card mx-3 mt-5" style="width: 25rem;"><div class="card-body row"><div class="col"><div class="card-icon position-relative g3"><i class="fas fa-coins text-white"></i></div></div><div class="col"><h5 class="card-title">ยอดเรียกเก็บ</h5><p class="card-text">{{(doc.amounts12mo.map(el=>(el.total??0)).reduce((a,b)=>a+b,0)).formatFull()}} บาท</p></div><hr class="mt-3"><p class="card-text">***</p></div></div><div class="card dashboard-card mx-3 mt-5" style="width: 25rem;"><div class="card-body row"><div class="col"><div class="card-icon position-relative g4"><i class="fas fa-sack text-white"></i></div></div><div class="col"><h5 class="card-title">ยอดค่าเรียกเก็บที่ชำระแล้ว</h5><p class="card-text">{{(doc.paid12mo.map(el=>(el.total??0)).reduce((a,b)=>a+b,0)).formatFull()}} บาท</p></div><hr class="mt-3"><p class="card-text">***</p></div></div><div class="card dashboard-card mx-3 mt-5" style="width: 25rem;"><div class="card-body row"><div class="col"><div class="card-icon position-relative g4"><i class="fas fa-file-invoice text-white"></i></div></div><div class="col"><h5 class="card-title">ค่าบำบัดน้ำเสียที่ยังจัดเก็บไม่ได้</h5><p class="card-text">{{((doc.amounts12mo.map(el=>(el.total??0)).reduce((a,b)=>a+b,0)-doc.paid12mo.map(el=>(el.total??0)).reduce((a,b)=>a+b,0))??0).formatFull()}} บาท</p></div><hr class="mt-3"><p class="card-text">***</p></div></div></div><div class="container-fluid px-5 row"><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart1"></canvas><hr class="mt-3"><p class="card-text">แสดงปริมาณการใช้น้ำย้อนหลัง</p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart2"></canvas><hr class="mt-3"><p class="card-text">แสดงค่าเรียกเก็บที่เรียกเก็บย้อนหลัง</p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart3"></canvas><hr class="mt-3"><p class="card-text">แสดงจำนวนผู้ใช้น้ำจำแนกเป็นประเภทย้อนหลัง</p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart4"></canvas><hr class="mt-3"><p class="card-text">แสดงค่าเรียกเก็บที่เรียกเก็บจำแนกเป็นประเภทย้อนหลัง</p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart5"></canvas><hr class="mt-3"><p class="card-text">แสดงยอดชำระที่เรียกเก็บจำแนกเป็นประเภทย้อนหลัง</p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart6"></canvas><hr class="mt-3"><p class="card-text">เปรียบเทียบยอดที่ต้องชำระกับยอดที่ชำระแล้วต่อเดือนแบ่งตามประเภท</p></div></div></div>