<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4" v-if="this.$route.params.id == undefined">เพิ่มรหัสบัญชี</h2><h2 class="text-decoration-underline mb-4" v-else>แก้ไขรหัสบัญชี</h2><div class="mb-3 mt-5"><label class="form-label">รหัสบัญชี</label><input class="form-control" type="text" placeholder=" " v-model="doc.code"></div><div class="mb-3"><label class="form-label">ชื่อบัญชี</label><input class="form-control" type="text" placeholder=" " v-model="doc.name"></div><div class="mb-3"><label class="form-label">หมวดค่าใช้จ่าย</label><select class="form-select" v-model.number="doc.category"><option :value="1">หมวดที่ 1: เงินเดือน (ผจก.,พนง.ลูกจ้าง)</option><option :value="2">หมวดที่ 2: ค่าซ่อมแซมและบำรุงรักษาระบบบำบัดน้ำเสีย</option><option :value="3">หมวดที่ 3: ค่าสาธารณูปโภค</option><option :value="4">หมวดที่ 4: ค่าใช้จ่ายอื่นๆ</option></select></div><div class="row mb-5"> </div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" :to="`/portal/code/list/`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></form></div>