
import address from "@/variable";
import axios from "axios";
import { Options, Vue } from "vue-class-component";
import { useStore } from "vuex";
import { Watch } from "vue-property-decorator";
import { DateTime, Interval, Duration } from "luxon";
@Options({})
export default class ConditionForm extends Vue {
  store = useStore();
  area: any = {};
  doc: any = {
    conditions:[]
  };
  isExisted: boolean = false;
  copySource: any = {};
  showDescription: boolean = true;
  DateTime = DateTime;
  zero:Number = 0

  print(){
    var css = ""
    if(this.showDescription){
      css = '@page { size: landscape; }'
    }else{
      css = '@page { size: portrait; }'
    }
    var head = document.head || document.getElementsByTagName('head')[0],
    style = document.createElement('style') as any

    style.type = 'text/css';
    style.media = 'print';

    if (style.styleSheet){
      style.styleSheet.cssText = css;
    } else {
      style.appendChild(document.createTextNode(css));
    }

    head.appendChild(style);
    window.print();
  }
  getHowToCalc(i:number){
    function getQuarter(d:Date) {
      d = d || new Date();
      var m = Math.floor(d.getMonth()/3) + 2;
      return m > 4? m - 4 : m;
    }
    let operationDate = DateTime.fromISO(this.doc.operationDate)
    let startYear = operationDate.toObject().year
    let currentYear = operationDate.toObject().year + i
    let currentYearOperationDate = operationDate.set({year:currentYear-1})
    let q = getQuarter(currentYearOperationDate.toJSDate())
    var startDate = currentYearOperationDate.startOf('quarter')
    var endDate = currentYearOperationDate.endOf('quarter')
    let beforeInterval = Math.abs(Math.round(startDate.diff(currentYearOperationDate,'day').toObject().days))
    let afterInterval = Math.abs(Math.round(endDate.diff(currentYearOperationDate,'day').toObject().days))
    let interval = beforeInterval + afterInterval
    let annual = ((this.doc.conditions[i]??{}).contributionLimit)??0
    let quarter = (((this.doc.conditions[i]??{}).contributionLimit)??1) / 4
    let pannual = (this.doc.conditions[i-1]??{}).contributionLimit
    let pquarter = (((this.doc.conditions[i-1]??{}).contributionLimit)??1) / 4
    if(i == this.doc.operationYear - 1) {
      return "ปีแรกที่เริ่มดำเนินการ"
    }else if(pannual==annual){
      return `อปท. พึงสมทบปีละ ${(annual??0).formatFull()} บาทคิดเป็นไตรมาสละ ${(quarter??0).formatFull()} บาท`
    }else{
      let changeDate = new Date(currentYearOperationDate).toThaiShort()
      let text = `ไตรมาสที่ ${q} มี ${interval} วัน`
      text += `ก่อนวันที่ ${changeDate} อปท. พึงสมทบปีละ ${pannual.formatFull()} บาท คิดเป็นไตรมาสละ ${pquarter} บาท คิดเป็นวันละ ${pquarter}/${interval} = ${((pquarter/interval)??0).formatFull()}บาท / `
      text += `ตั้งแต่วันที่ ${new Date(currentYearOperationDate.minus({day:beforeInterval})).toThaiShort()} จนถึงวันที่ ${changeDate} คิดเป็น ${beforeInterval} วัน เป็นจำนวนเงิน ${((pquarter/interval)??0).formatFull()} x ${beforeInterval} = ${((((pquarter/interval)??0)*beforeInterval)??0).formatFull()} บาท / `
      text += `หลังวันที่ ${changeDate} อปท. พึงสมทบปีละ ${annual.formatFull()} บาทคิดเป็นไตรมาสละ ${quarter} บาท คิดเป็นวันละ ${quarter}/${interval} = ${((quarter/interval)??0).formatFull()}บาท / `
      text += `ตั้งแต่วันที่ ${changeDate} จนถึงวันที่ ${new Date(currentYearOperationDate.plus({day:afterInterval})).toThaiShort()} คิดเป็น ${afterInterval} วัน เป็นจำนวนเงิน ${((quarter/interval)??0).formatFull()} x ${afterInterval} = ${((((quarter/interval)??0)*afterInterval)??0).formatFull()} บาท / `
      text += `${((((pquarter/interval)??0)*beforeInterval)??0).formatFull()} + ${((((quarter/interval)??0)*afterInterval)??0).formatFull()} = ${(((((pquarter/interval)??0)*beforeInterval)??0) + ((((quarter/interval)??0)*afterInterval)??0)).formatFull()}`
      return text
    }
  }
  getAnnualAmount(endDate:any){
    var operationDate = DateTime.fromISO(this.doc.operationDate)
    let duration = endDate.diff(operationDate,'year').toObject().years
    let calcYear = Math.ceil(duration)
    let yearAmount
    if(calcYear>=this.doc.conditions.length && calcYear>this.doc.operationYear){
      let annual = this.doc.conditions.slice(-1).contributionLimit??0
      yearAmount = annual
    }
    else{
      let annual = this.doc.conditions[calcYear]??{}
      yearAmount = annual.contributionLimit??0
    }
    return yearAmount
  }

  calcMonth(month:number,year:number){
    var operationDate = DateTime.fromISO(this.doc.operationDate)
    let startYear = operationDate.toObject().year
    //@ts-ignore
    let currentYear = operationDate.toObject().year + year
    //@ts-ignore
    var startDate = DateTime.fromObject({year:startYear+year,month:month}).startOf("month")
    //@ts-ignore
    var endDate = DateTime.fromObject({year:startYear+year,month:month}).endOf("month")
    let duration = endDate.diff(operationDate,'year').toObject().years
    //@ts-ignore
    let calcYear = Math.ceil(duration)
    let interval = Interval.fromDateTimes(startDate, endDate);
    //@ts-ignore
    let intervalDay = Math.ceil(interval.toDuration("days").toObject().days);

    let yearAmount
    if(calcYear>=this.doc.conditions.length && calcYear>this.doc.operationYear){
      let annual = this.doc.conditions.slice(-1).contributionLimit??0
      yearAmount = annual
    }
    else{
      let annual = this.doc.conditions[calcYear]
      yearAmount = annual.contributionLimit??0
    }
    let currentYearOperationDate = operationDate.set({year:currentYear})
    let beforeInterval = startDate.diff(currentYearOperationDate,'day').toObject().days
    let afterInterval = endDate.diff(currentYearOperationDate,'day').toObject().days
    // return yearAmount + ":" + intervalDay + "/" + beforeInterval + "*" + afterInterval
    //@ts-ignore
    if(beforeInterval<0&&afterInterval<0){
      return parseFloat((yearAmount/intervalDay).toFixed())
    }
    //@ts-ignore
    else if(beforeInterval>=0&&afterInterval>=0){
      return parseFloat((yearAmount/intervalDay).toFixed())
    }
    else{
      return 9999//"ตรงกลาง"
    }
    return {beforeInterval, afterInterval}
    // return endDate.toISO()
  }
  calcQuarter(month:number,year:number){
    var operationDate = DateTime.fromISO(this.doc.operationDate)
    //@ts-ignore
    let currentYear = operationDate.toObject().year + year
    let currentYearOperationDate = operationDate.set({year:currentYear})
    let startYear = operationDate.toObject().year
    //@ts-ignore
    var startDate = DateTime.fromObject({year:startYear+year,month:month}).startOf("month")
    //@ts-ignore
    var endDate = DateTime.fromObject({year:startYear+year,month:month+2}).endOf("month")
    let interval = Interval.fromDateTimes(startDate, endDate);
    //@ts-ignore
    let intervalDay = Math.ceil(interval.toDuration("days")!.toObject().days);
    let annualAmount = this.getAnnualAmount(endDate)

    let beforeInterval = startDate.diff(currentYearOperationDate,'day').toObject().days
    let afterInterval = endDate.diff(currentYearOperationDate,'day').toObject().days


    let prevAnnualAmount = 0
    try {
       prevAnnualAmount = this.getAnnualAmount(endDate.minus({year:1}))
    } catch (error) {
      
    }
    let nextAnnualAmount = this.getAnnualAmount(endDate.plus({year:1}))
    let quarterAmount = prevAnnualAmount/4
    let nextQuarterAmount = annualAmount/4
    //@ts-ignore
    let before = Math.ceil(Math.abs(beforeInterval))
    //@ts-ignore
    let after = Math.ceil(Math.abs(afterInterval))
    let total = before + after
    let beforeQuarter = ((quarterAmount??0)/total)*before
    let afterQuarter = quarterAmount
    let beforeAmount = quarterAmount/total*before
    let afterAmount = nextQuarterAmount/total*after
    
    // return endDate.toObject().year+543
//@ts-ignore
    if(endDate.diff(operationDate,'day').toObject().days<0) return 0
    //@ts-ignore
    else if(endDate.diff(operationDate,'day').toObject().days>0 && startDate.diff(operationDate,'day').toObject().days<0) {
        beforeAmount = 0
        return parseFloat((beforeAmount+afterAmount).toFixed(2))
    }
    //@ts-ignore
    else if(beforeInterval<0&&afterInterval<0){
      quarterAmount = annualAmount/4
      return parseFloat((quarterAmount).toFixed())
    }
    //@ts-ignore
    else if(beforeInterval>=0&&afterInterval>=0){
      quarterAmount = annualAmount/4
      return parseFloat((quarterAmount).toFixed())
    }
    else{
      return parseFloat((beforeAmount+afterAmount).toFixed(2))
      // if(quarterAmount==nextQuarterAmount) return parseFloat((beforeAmount+afterAmount).toFixed(2))
      // else
      // return {
      //   b:quarterAmount + "/" + total + "*" + before,
      //   a:nextQuarterAmount + "/" + total + "*" + after,
      // }
    }
    return {quarterAmount,beforeInterval,afterInterval}
  }

  resolveRange(doc: any, q: number, i: number) {
    let from: any;
    let to: any;
    var operationDate = DateTime.fromISO(this.doc.operationDate) 
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    let calcYearPlus = (i - (this.doc.operationYear??0))??0;
    // console.log(this.doc.operationYear??0)
    switch (q) {
      case 1:
        from = operationDate 
          .set({ month: 10 })
          .plus({ years: (calcYearPlus??2020) })
          .startOf("month");
        to = operationDate
          .set({ month: 12 })
          .plus({ years: (calcYearPlus??2020) })
          .endOf("month");
        break;
      case 2:
        from = operationDate
          .set({ month: 1 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .startOf("month");
        to = operationDate
          .set({ month: 3 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .endOf("month");
        break;
      case 3:
        from = operationDate
          .set({ month: 4 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .startOf("month");
        to = operationDate
          .set({ month: 6 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .endOf("month");
        break;
      case 4:
        from = operationDate
          .set({ month: 7 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .startOf("month");
        to = operationDate
          .set({ month: 9 })
          .plus({ years: (calcYearPlus??2020) + 1 })
          .endOf("month");
        break;
      default:
        break;
    }
    return {
      from,
      to,
    };
  }

  getRange(doc: any, q: number, i: number) {
    let { from, to } = this.resolveRange(doc, q, i);
    let fText = from.toFormat("d LLL yy")
    let tText = to.toFormat("d LLL yy")
    if(from.toFormat("d LLL yy") == "Invalid DateTime") fText = ""
    if(to.toFormat("d LLL yy") == "Invalid DateTime") tText = ""
    return `${fText} - ${tText}`;
  }

  calcLast(q: number) {
    let i = (this.doc.conditions??[]).length-1;
    let doc = this.doc
    let dBefore = 0;
    let dAfter = 0;
    let aBefore = 0;
    let aAfter = 0;
    var operationDate = DateTime.fromISO(this.doc.operationDate)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    var contractEnd = DateTime.fromISO(this.area.contractEnd)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    let calcYearPlus = contractEnd.toObject().year;
    let { from, to } = this.resolveRange(doc, q, i);
    if (q == 1) {
      //@ts-ignore
      from = from.set({ year: (calcYearPlus??2020)-1 });
      //@ts-ignore
      to = to.set({ year: (calcYearPlus??2020)-1 });
    } else {
      from = from.set({ year: (calcYearPlus??2020) });
      to = to.set({ year: (calcYearPlus??2020) });
    }
    let interval = Interval.fromDateTimes(from, to);
    //@ts-ignore
    let intervalDay = Math.round(interval.toDuration("days").toObject().days);
    dBefore = Math.round(from.diff(contractEnd, "days").toObject().days);
    dAfter = Math.round(to.diff(contractEnd, "days").toObject().days);
    let cur = 0
    if(i!=-1){
      cur = Math.round(
        ((doc.conditions[i] ?? { contributionLimit: 0 }).contributionLimit??0 /
          4) *
          100
      ) / 100;
    }
    
    aBefore = Math.round((cur / (intervalDay / dBefore)) * 100);
    aAfter = 0
    if(dBefore>0) return 0
    else{
      if (interval.contains(contractEnd)) {
        if(to.hasSame(contractEnd, 'day')){
          return cur
        }
        else{
          return Math.round((cur / (intervalDay / dBefore)) * 100) / 100
        }
        console.log("isSame",to.hasSame(contractEnd, 'day'))
          console.log("dAfter2",dBefore,dAfter,intervalDay)
        return aBefore;
      } else {
        return cur;
      }
    }
  }
  calc(doc: any, q: number, i: number) {
    var operationDate = DateTime.fromISO(this.doc.operationDate)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    var nextOptDate = DateTime.fromISO(this.doc.operationDate)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th")
      .plus({ years: i - this.doc.operationYear + 1 });

    let calcYearPlus = i - this.doc.operationYear;
    let { from, to } = this.resolveRange(doc, q, i);
    let interval = Interval.fromDateTimes(from, to);
    //@ts-ignore
    let intervalDay = Math.round(interval.toDuration("days").toObject().days);
    let cur = 0;
    let next = 0;
    cur =
      Math.round(
        ((doc.conditions[i] ?? { contributionLimit: 0 }).contributionLimit /
          4) *
          100
      ) / 100;
    next =
      Math.round(
        ((doc.conditions[i + 1] ?? { contributionLimit: 0 }).contributionLimit /
          4) *
          100
      ) / 100;

    let calcInterval = () => {
      let dBefore = 0;
      let dAfter = 0;
      let aBefore = 0;
      let aAfter = 0;
      dBefore = Math.abs(
        Math.round(from.diff(nextOptDate, "days").toObject().days)
      );
      dAfter = Math.abs(
        Math.round(to.diff(nextOptDate, "days").toObject().days)
      );
      if (i + 1 == this.doc.operationYear) {
        aBefore = 0;
        aAfter = Math.round((cur / (intervalDay / dAfter)) * 100);
      } else if (i + 1 != this.doc.contractYear) {
        aBefore = Math.round((cur / (intervalDay / dBefore)) * 100);
        aAfter = Math.round((next / (intervalDay / dAfter)) * 100);
      } else {
        aBefore = Math.round((cur / (intervalDay / dBefore)) * 100);
        aAfter = Math.round((cur / (intervalDay / dAfter)) * 100);
      }
      return ((aBefore || 0) + (aAfter || 0)) / 100;
    };
    if (i + 1 < this.doc.operationYear) {
      return 0;
    } else if (i + 1 == this.doc.operationYear) {
      if (interval.contains(nextOptDate)) {
        return calcInterval();
      } else {
        return 0;
      }
    } else {
      if (interval.contains(nextOptDate)) {
        return calcInterval();
      } else {
        return cur;
      }
    }
    // if (i + 1 < this.doc.operationYear) {
    //   return 0;
    // }
    // else if (i + 1 == this.doc.operationYear) {
    //   if (interval.contains(nextOptDate)) {
    //     return calcInterval();
    //   } else {
    //     return 0;
    //   }
    // } else {
    //   if (interval.contains(nextOptDate)) {
    //     return calcInterval();
    //   } else {
    //     return isNaN(cur) ? 0 : cur ?? 6;
    //   }
    // }
  }
  dateRange(dt: Date, i: number) {
    //@ts-ignore
    var dtThai = DateTime.fromISO(dt)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    let result = dtThai
      .plus({ years: i - this.doc.operationYear + 1 })
      .toFormat("dd LLL yy");
    result += " - ";
    if (i + 1 == this.doc.contractYear) {
      result += DateTime.fromISO(this.area.contractEnd)
        .reconfigure({ outputCalendar: "buddhist" })
        .setLocale("th")
        .toFormat("dd LLL yy");
    } else {
      result += dtThai
        .plus({ years: i - this.doc.operationYear + 2, days: -1 })
        .toFormat("dd LLL yy");
    }
    return result;
  }
  yearCollect(dt: Date, i: number) {
    //@ts-ignore
    var dtThai = DateTime.fromISO(dt)
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th");
    let result = dtThai
      .plus({ years: i - this.doc.operationYear + 1 })
      .toFormat("yy");
    return result;
  }
  mounted() {
    if (this.$route.params.id != undefined) this.getData();
  }
  getData() {
    axios.get(`${address}/area/${this.$route.params.id}`).then((response) => {
      this.area = response.data;
      axios
        .get(`${address}/areacondition-by-field/area/${this.$route.params.id}`)
        .then((response) => {
          if (response.data == "") {
            this.doc = {};
            this.doc.name = this.area.prefix + this.area.name;
            this.doc.area = this.area._id;
            this.doc.contractYear = this.area.contractYear;
          } else {
            this.doc = response.data;
            this.doc.name = this.area.prefix + this.area.name;
            this.doc.area = this.area._id;
            this.isExisted = true;
          }
        });
    });
  }
}
