<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='ผลการจัดเก็บค่าบริการ')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">ผลการจัดเก็บค่าบริการ</h2><div class="col-12 d-flex justify-content-start my-3"><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row d-print-none"><div class="col-2"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" v-model.number="search.fromYear"> <option v-for="item,i in 10">{{new Date().getFullYear()+543-i}}</option></select></div><div class="col-1 mt-5 text-center"><span>ถึง</span></div><div class="col-2"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" v-model.number="search.toYear"> <option v-for="item,i in 10">{{new Date().getFullYear()+543-i}}</option></select></div><div class="col-1"></div><div class="col-6"><label class="mt-3">พื้นที่</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"><option :value="rates.map(el=&gt;el.code)">ทั้งหมด</option><option v-for="item in rates" :value="[item.code]">{{item.name}} ({{item.code}})</option></select></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="postData()">ค้นหา</button></div></div><div class="row justify-content-center"><h4 class="text-center" v-if="search.code!=undefined"><span v-if="this.search.code.length &gt; 1">พื้นที่ อจน.จัดเก็บทั้งหมด</span><span v-else>พื้นที่ อจน.จัดเก็บ ({{(rates.find(el=>el.code==search.code)??{}).name}}) </span></h4><span class="text-center" v-if="search.fromYear!=undefined">({{search.fromYear}} - {{search.toYear}}) </span><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><p class="card-text text-center">ผลการจัดเก็บสะสมการชำระค่าบริการบำบัดน้ำเสีย {{(rates.find(el=>el.code==search.code)??{}).name}}</p><hr class="mt-1"><canvas ref="chart"></canvas></div></div></div><div class="col-12 p-2"><div class="col-12 p-2 mt-3"><table class="table table-striped table-hover"><thead class="table-dark text-center"><tr class="text-center"><th class="bg-primary" scope="col">เรียกเก็บ</th><th class="bg-danger" scope="col">หนี้ค้างชำระสะสม</th><th class="bg-secondary" scope="col">ชำระสะสม</th><th class="bg-warning" scope="col">คิด %</th></tr></thead><tbody><tr class="text-center"><th scope="row">{{(getValue(doc.totalAmount)*1.07).formatFull()}}</th><td>{{getValue(doc.debtAmount).formatFull()}}</td><td>{{getValue(doc.paymentAmount).formatFull()}}</td><td> <span v-if="doc.totalAmount==undefined"></span><span v-else>{{(((getValue(doc.paymentAmount)*100)??0)/(((getValue(doc.totalAmount)*1.07)??0)+(getValue(doc.paymentAmount))??0)).formatFull()}} %</span></td></tr></tbody></table></div></div></div>