<NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'ต้นทุนปันส่วนน้ำ') ==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการต้นทุนปันส่วนน้ำ</h2><label class="d-print-none">ค้นหาพื้น:</label><form class="d-flex d-print-none" @submit.prevent="fetchData()"><input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" v-model="search.name"><button class="btn btn-outline-success" type="submit">ค้นหา</button></form><div class="row my-2"><div class="col d-flex align-items-center mt-1 justify-content-end"><button class="btn btn-outline-success mx-2" id="clear-btn" type="submit" @click="clearSearch();">ล้างการค้นหา</button></div></div><div class="col-12 d-flex justify-content-end mt-3"> <router-link class="btn btn-info mx-3" to="/portal/collection/arearation/form">สร้าง</router-link></div><div class="row mt-2"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"><th>ลำดับ</th><th>พื้นที่</th><th>อัตราการคิดค่าบริการ</th><th>ปี</th><th><i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody class="align-middle text-center"><template v-for="item,i in docs"><tr><th scope="row">{{(page*show)+(i+1)}}</th><td>{{(item.area??{}).name}}</td><td>{{item.rate}}</td><td>{{item.year}}</td><td><router-link class="btn btn-info me-2" :to="`/portal/collection/arearation/edit/${item._id}`"> <i class="class fas fa-pencil"></i></router-link><a class="btn btn-danger" @click="deleteData(item._id)"><i class="fas fa-trash-alt"></i></a></td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>