<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='ทะเบียนคุมใบคำร้อง')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">ทะเบียนคุมใบคำร้อง</h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button><div class="col-12 p-2"><div class="col-12 p-2 mt-3"><table class="table table-striped table-hover"><thead class="table-dark text-center"><tr class="text-center"><th scope="col">ลำดับ</th><th scope="col">คำขอ</th><th scope="col">สถานะ</th><th scope="col">ชื่อผู้ใช้ (Username)</th><th scope="col">ชื่อ</th><th scope="col">ข้อมูล</th><th scope="col">ข้อมูลเดิม</th><th scope="col">ข้อมูลที่แก้ไข</th><th scope="col">หมายเหตุ</th><th scope="col">เอกสารแนบ</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}</th><td>{{item.requestType}}</td><td>{{item.status}}</td><td class="text-start"> <span v-if="item.requester!=undefined"> <span class="me-2">{{item.requester.firstName}} </span><span class="me-2">{{item.requester.lastName}}</span><span>- {{item.requester.position}}</span></span></td><td class="text-start">                <span v-if="item.from!=undefined">{{item.from.name}}</span></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">ปริมาณการใช้น้ำ</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">ประเภทการคำนวณ</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">ประเภท</li></ul></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">{{item.from.qty}}</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">{{item.from.categoryType}}</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">{{item.from.category}}</li></ul></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">{{item.to.qty}}</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">{{item.to.categoryType}}</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">{{item.to.category}}</li></ul></td><td> <span>{{item.note}}</span></td><td> <a :href="`${filePath}/${item.fileUrl}`">{{item.fileUrl}}</a></td></tr></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>