<div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span>กำลังดึงข้อมูลจากแม่ข่าย</span></div></transition></div><NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'บันทึกการรับเงิน อปท.') ==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">บันทึกการรับเงิน อปท.</h2><div class="col-12 my-3"><label>พื้นที่</label><select id="area-select" v-model="search.area"><option selected="">จังหวัด</option><template v-for="area in areas"><option :value="area._id">{{area.name}}</option></template></select></div><div class="d-flex justify-content-end"><button class="btn btn-outline-success" @click="fetchData()">ค้นหา</button></div><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12"><div class="form-check form-switch"><input class="form-check-input" id="showDetail" type="checkbox" v-model="showDetail"><label class="form-check-label" for="showDetail">แสดงรายละเอียด</label></div></div><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center"><th class="align-middle">ลำดับ</th><th class="align-middle">ประเภท</th><th class="align-middle">พื้นที่</th><th class="align-middle">จำนวนปีตามสัญญา</th><th class="align-middle">ปัจจุบันเป็นปีที่</th><td class="align-middle">ไตรมาส</td><td class="align-middle">ตามเงื่อนไข</td><td class="align-middle">วันที่ชำระเงิน</td><td class="align-middle">จำนวน</td><td class="align-middle">แนบเอกสาร</td><th class="align-middle"><i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody><template v-for="(item,i) in sorted"><template v-if="item.collections.length&gt;0"><template v-for="(element,j) in item.collections"><tr class="text-center" v-if="!showDetail?j==0:true"><th scope="row"> <span v-if="j==0">{{i+1}}</span></th><td> <span v-if="j==0">{{item.prefix??""}}</span></td><td> <span v-if="j==0">{{item.name??""}}</span></td><td>{{item.contractYear}}</td><td>{{new Date().getFullYear() - new Date(item.contractStart).getFullYear()}}</td><td></td><td> <span v-if="item.conditions==undefined"> <div class="text-muted small">กรุณาเพิ่มข้อมูล</div></span><span v-else>{{getCondition(item.conditions,new Date().getFullYear() - new Date(item.contractStart).getFullYear())}}</span></td><td><span>{{element.recordDate != undefined ? new Date(element.recordDate).toThaiShort() : "-"}}</span></td><td><span>{{(element.amount??0).formatFull()}}  บาท</span></td><td><ul class="list-group"> <li class="list-group-item" v-for="file in element.fileUrl"><a :href="`${filePath}/${file}`"><i class="fas fa-cloud me-3"></i><span>{{file}}</span></a></li></ul></td><td class="text-center"><router-link class="btn btn-warning" :to="`/portal/collection/form/${item._id}`"><span>แก้ไข</span></router-link></td></tr></template></template><template v-else><tr class="text-center"><th scope="row">{{i+1}}</th><td> <span>{{item.prefix??""}}</span></td><td> <span>{{item.name??""}}</span></td><td>{{item.contractYear}}</td><td>{{new Date().getFullYear() - new Date(item.contractStart).getFullYear()}}</td><td></td><td><span v-if="item.conditions==undefined"> <div class="text-muted small">กรุณาเพิ่มข้อมูล</div></span><span v-else>{{getCondition(item.conditions,new Date().getFullYear() - new Date(item.contractStart).getFullYear())}} </span></td><td></td><td></td><td> </td><td class="text-center"><router-link class="btn btn-warning" :to="`/portal/collection/form/${item._id}`"><span>แก้ไข</span></router-link></td></tr></template></template></tbody></table></div></div><nav><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>