<!--modal--><div class="modal fade" id="exampleEstimationModal" tabindex="-1" aria-labelledby="exampleEstimationModalLabel" aria-hidden="true"><div class="modal-dialog modal-dialog-centered" style="width:50em"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="exampleModalLabel">จัดทำกรอบ 2</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="row"><div class="col d-flex justify-content-center"><div class="form-check"><input class="form-check-input" id="flexRadioDefault1" type="radio" value="new" v-model="mode" name="flexRadioDefault"><label class="form-check-label" for="flexRadioDefault1">จัดทำกรอบ 2 ใหม่</label></div><div class="form-check mx-5"><input class="form-check-input" id="flexRadioDefault2" type="radio" value="edit" v-model="mode" name="flexRadioDefault" checked=""><label class="form-check-label" for="flexRadioDefault2">จัดทำกรอบ 2 ล่าสุด</label></div></div></div></div><div class="modal-footer"><div class="col d-flex justify-content-center"><button class="btn btn-light mx-3" type="button" data-bs-dismiss="modal" ref="cancelModal">ยกเลิก</button><button class="btn btn-success" type="button" @click="goToForm()">ยืนยัน</button></div></div></div></div></div><div class="modal fade" id="exampleEstimationLsitModal" tabindex="-1" aria-labelledby="exampleEstimationLsitModalLabel" aria-hidden="true"><div class="modal-dialog modal-lg"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="exampleModalLabel">รายการกรอบ 2 ของ {{area}} ปี {{year}} <template v-if="quarter != 0"><span>ไตรมาส {{quarter}}</span></template></h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><table class="table"><thead><tr><th scope="col">ลำดับ</th><th scope="col">วันที่จัดทำ</th><th scope="col">วันที่ปรับปรุง</th><th scope="col"> <i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody v-for="(item,i) in activeCalculations"><tr><th scope="row">{{i+1}}</th><td> <span>{{new Date(item.createdAt).toThaiShort()}}</span></td><td> <span v-if="item.modifiedAt!=undefined">{{new Date(item.modifiedAt).toThaiShort()}}</span><span v-else>-</span></td><td> <button class="btn btn-danger" type="button" @click="id=item._id; goToPDF()">PDF</button></td></tr></tbody></table></div><div class="modal-footer"><div class="col d-flex justify-content-center"><button class="btn btn-light mx-3" type="button" data-bs-dismiss="modal" ref="cancelModal2">ยกเลิก</button><button class="btn btn-success" type="button">ยืนยัน</button></div></div></div></div></div><NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'ค่าใช้จ่าย (กรอบ 2)') ==-1"></NoAccess><div class="container-fluid p-4" v-else><StepIncome step="1"></StepIncome><h2 class="text-decoration-underline mb-4">รายการประมาณการค่าใช้จ่ายในการบริหารจัดการระบบบำบัดน้ำเสีย</h2><h5 class="text-muted">(รายการกรอบ 2)</h5><div class="col-12 d-flex justify-content-end"> </div><JMIncomeStepSearch v-model="search" @clear="search={}; fetchData();" @search="fetchData();"></JMIncomeStepSearch><div class="d-flex justify-content-end mt-3"><router-link class="btn btn-success text-white mx-2" type="submit" to="/portal/sao/import">เพิ่มข้อมูล สตง.</router-link></div><label>จำนวนที่แสดง       </label><div class="row justify-content-between"><div class="col-2"> <select class="form-select form-select-sm mb-2" v-model.number="show" @change="fetchData()"> <option selected="" value="999999">ทั้งหมด</option><option value="50">50</option></select></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center align-middle"><th scope="col" rowspan="2">ลำดับ</th><th scope="col" rowspan="2">ประเภท</th><th scope="col" rowspan="2">พื้นที่</th><th scope="col" rowspan="2">ปี</th><th scope="col" rowspan="2">ไตรมาส</th><th scope="col" colspan="2">รายละเอียดบัญชี</th><th scope="col" rowspan="2">กรอบ 2</th></tr><tr class="text-center"><th>Updated</th><th>...</th></tr></thead><tbody><tr class="text-center align-middle" v-for="item,i in docs"><th scope="row"> <div class="row"><span>{{(page*show)+(i+1)}}  </span><i class="fas fa-check-circle text-success" v-if="item.modifiedDate!=undefined"></i></div></th><td>{{item.prefix??""}}</td><td>{{item.name??"-"}}</td><td>{{item.year}}</td><td> <span v-if="item.quarter!=0">{{item.quarter}}</span><span v-else>รายปี</span></td><td> <span>{{toThaiFormat(item.calculations)}}</span></td><td><button class="btn btn-success text-white mx-3" ref="callModal" type="button" data-bs-toggle="modal" data-bs-target="#exampleEstimationModal" @click="parameter = `${item.area}/${item.year}/${item.j}/${item.quarter}?searchparam=${JSON.stringify(this.search)}`">จัดทำกรอบ 2</button><button class="btn btn-primary text-white mx-3" ref="callModal" type="button" data-bs-toggle="modal" data-bs-target="#exampleEstimationLsitModal" @click="activeCalculations = item.calculations; area=item.name; year=item.year; quarter=item.quarter">ประวัติ</button></td><td><router-link class="btn btn-danger" v-if="(item.calculations??[]).length&gt;0" :to="`/portal/estimation/pdf-id/${item.calculations[0]._id}`" target="_blank">PDF</router-link><a class="btn btn-success ms-3 text-white" v-if="(item.calculations??[]).length&gt;0" @click="downloadExcel(item.calculations[0],'ledgers')">XLSX ก่อนสตง.รับรอง</a><a class="btn btn-success ms-3 text-white" v-if="(item.calculations??[]).length&gt;0" @click="downloadExcel(item.calculations[0],'eLedgers')">XLSX หลังสตง.รับรอง</a></td></tr></tbody></table></div></div><nav><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>