
import { Options, Vue } from "vue-class-component";
import { useStore } from "vuex";
import address from "@/variable";
import axios from "axios";
import Excel from "exceljs/dist/es5/exceljs.browser";
import { DateTime } from "luxon"

@Options({
  components: {
  },
})
export default class PaymentImport extends Vue {
  store = useStore();
  docs:Array<any> = []
  meter: string= "";
  code: string= "";
  invoiceAmount: string="";
  // year = new Date().getFullYear() + 543
  // month = new Date().getMonth() + 1
  year: number = 0;
  month: number = 0;
  async importExcel(ev: any) {
    const Excel = require("exceljs/dist/es5/exceljs.browser");
    let self = this;
    console.log(ev)
    let file = ev.target.files[0];
    const workbook = new Excel.Workbook();
    let book = await workbook.xlsx.load(file);
    let sheet = book.worksheets[0];
    let count = sheet.rowCount;
    let arr: Array<any> = [];
    let result: Array<any> = [];
    let promises: Array<Promise<any>> = []
    sheet.eachRow(function(row: any, rowNumber: any) {
      if(rowNumber>1){
        console.log(row.getCell(2))
        console.log(row.getCell(2).value instanceof Date)
        let time = DateTime.fromJSDate(row.getCell(2).value).plus({hours:-7}).toFormat("HHmm")
        let timeInt = parseInt(time)
        arr.push({
          paymentDate:row.getCell("A").value,
          meter:row.getCell("B").value,
          name:row.getCell("C").value,
          address:row.getCell("D").value,
          paymentAmount:row.getCell("E").value,
          year:row.getCell("F").value,
          month:row.getCell("G").value,
        });
      }
    });
    arr.forEach(e=>{
      // promises.push(axios.get(`${address}/invoice-by-field/sequence/${e.invoiceNumber}`))
      promises.push(axios.post(`${address}/invoice-find`,{
        search: {
          meter: e.meter,
          year: e.year,
          month: e.month,
      },
      }))
    })
    Promise.all(promises).then(responses=>{
      console.log(responses)
      result = responses.map(res=>res.data)
      result.forEach((el,i)=>{
        result[i] = {
          ...arr.find(a=>(a.meter==el.meter&&a.year==el.year&&a.month==el.month)),
          ...el,
          invoiceNumber:el.sequence,
          // invoiceAmount:38736353,
          process:true
        }
      })
      this.docs = result
    })
    ev.target.value = null;
  }

  postData() {
    this.docs.forEach((item) => {
      console.log('item',item)
      axios
        .post(`${address}/payment-upsert`, item)
        .then((response) => {
          console.log(response);
        });
    });
    alert("ส่งข้อมูลไปยังแม่ข่ายแล้ว");
    this.$router.push("/portal/payment/list");
  }
}
