<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4" v-if="this.$route.params.id == undefined">แบบข้อมูลใช้น้ำ</h2><h2 class="text-decoration-underline mb-4" v-else>แก้ไขข้อมูลใช้น้ำ</h2><div class="mb-3"><label class="form-label">เลขที่ผู้ใช้น้ำ</label><input class="form-control" type="text" placeholder=" " v-model="doc.meter"></div><div class="mb-3"><label class="form-label">ชื่อ</label><input class="form-control" type="text" placeholder=" " v-model="doc.name"></div><div class="mb-3"><label class="form-label">ที่อยู่</label><input class="form-control" type="text" placeholder=" " v-model="doc.address"></div><div class="row"><div class="col-6"><label class="mt-3">ข้อมูลประจำปี</label><select class="form-select form-select mb-3" v-model.number="doc.year"> <option selected="">ปี</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6">   <label class="mt-3">ข้อมูลปริมาณน้ำประจำเดือน</label><select class="form-select form-select mb-3" v-model.number="doc.month" placeholder="เดือน"> <option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div></div><div class="mb-3"><label class="form-label">ปริมาณการใช้น้ำ (ลบ.ม.)</label><input class="form-control" type="number" placeholder=" " v-model="doc.qty"></div></form><div class="row"><label class="mt-3">รหัสสายจัดเก็บ/พื้นที่จัดเก็บ</label><div class="col-6"><select class="form-select form-select mb-3" v-model="doc.code"><option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="mb-3">  <div class="col-6"><div class="row mb-3"> <label>ประเภทผู้ใช้น้ำ {{doc.rate}}</label><div class="form-check"><input class="form-check-input" id="treatmentType1" type="radio" name="treatmentType1" value="1,น้ำเสีย" v-model="doc.cat"><label class="form-check-label" for="treatmentType1">ค่าบำบัดน้ำเสียประเภทที่ 1 ({{((activeContract??{}).treatment1??0).formatFull()}} {{((activeContract??{}).treatmentType)}})</label></div><div class="form-check"><input class="form-check-input" id="treatmentType2" type="radio" name="treatmentType2" value="2,น้ำเสีย" v-model="doc.cat"><label class="form-check-label" for="treatmentType2">ค่าบำบัดน้ำเสียประเภทที่ 2 ({{((activeContract??{}).treatment2??0).formatFull()}} {{((activeContract??{}).treatmentType)}})</label></div><div class="form-check"><input class="form-check-input" id="treatmentType3" type="radio" name="treatmentType3" value="3,น้ำเสีย" v-model="doc.cat"><label class="form-check-label" for="treatmentType3">ค่าบำบัดน้ำเสียประเภทที่ 3 ({{((activeContract??{}).treatment3??0).formatFull()}} {{((activeContract??{}).treatmentType)}})</label></div><div class="form-check"><input class="form-check-input" id="effluentType1" type="radio" name="effluentType1" value="1,น้ำทิ้ง" v-model="doc.cat"><label class="form-check-label" for="effluentType1">ค่าบำบัดน้ำทิ้งประเภทที่ 1 ({{((activeContract??{}).effluent1??0).formatFull()}} {{((activeContract??{}).effluentType)}})</label></div><div class="form-check"><input class="form-check-input" id="effluentType2" type="radio" name="effluentType2" value="2,น้ำทิ้ง" v-model="doc.cat"><label class="form-check-label" for="effluentType2">ค่าบำบัดน้ำทิ้งประเภทที่ 2 ({{((activeContract??{}).effluent2??0).formatFull()}} {{((activeContract??{}).effluentType)}})</label></div><div class="form-check"><input class="form-check-input" id="effluentType3" type="radio" name="effluentType3" value="3,น้ำทิ้ง" v-model="doc.cat"><label class="form-check-label" for="effluentType3">ค่าบำบัดน้ำทิ้งประเภทที่ 3 ({{((activeContract??{}).effluent3??0).formatFull()}} {{((activeContract??{}).effluentType)}})</label></div><div class="form-check"><input class="form-check-input" type="radio" name="4" value="4,เหมา" v-model="doc.cat" @click="doc.qty = 1"><label class="form-check-label">ค่าบำบัดน้ำเสียประเภทเหมาจ่าย </label><div class="col-5" v-if="doc.cat == '4,เหมา'"><div class="input-group mb-3"><input class="form-control" type="Number" placeholder="" v-model="doc.rate"><div class="input-group-append"><span class="input-group-text" id="basic-addon2">บาท/เดือน</span></div></div></div></div></div></div><div class="mb-3" v-if="invoice.isPrint"><div class="row"><label class="form-label">แนบใบคำร้อง <pre>{{doc.fileUrl}}</pre></label><JMUpload v-model="doc.fileUrl"></JMUpload></div><div class="mb-1"><label class="form-label">หมายเหตุ</label><input class="form-control" type="text" placeholder=" " v-model="doc.note"></div></div></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/usage/list?searchparam=${($route.query??{}).searchparam}`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" v-if="invoice.isPrint" @click="submitRequest()">บันทึกคำร้อง</button><button class="btn btn-success mx-2" type="button" v-else @click="submit()">บันทึก</button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></div></div>