<div class="container"><div class="row"><h2>สรุปผล ปัญหา อุปสรรค/แนวทางแก้ไข</h2><h4>พื้นที่ที่จ่ายไม่ตรงตามแผน ได้แก่</h4><ul class="list-group my-3"><template v-for="item in filtered"> <li class="list-group-item" v-if="item.problemType=='จ่ายไม่ตรงตามแผน'">{{item.area}}</li></template></ul><h4>พื้นที่ที่ไม่จ่ายตามแผน ได้แก่</h4><ul class="list-group my-3"><template v-for="item in filtered"> <li class="list-group-item" v-if="item.problemType=='ไม่จ่ายตามแผน'">{{item.area}}</li></template></ul><p>สรุปปัญหา อุปสรรค และแนวทางแก้ไข</p></div><table class="table"><thead><tr class="text-center"><th>#</th><th>พื้นที่</th><th>ปัญหา</th><th>รายละเอียดปัญหา</th><th>แนวทางแก้ไข</th><th> <i class="fas fa-ellipsis-h"></i></th></tr><tr class="text-center" v-for="item,i in filtered"><td>{{i+1}}</td><td>{{item.area}}</td><td>{{item.problemType}}</td><td>{{item.problem}}</td><td>{{item.solution}}</td><td><a @click="hideItem(item._id)"> <i class="fas fa-trash-alt text-danger"></i></a></td></tr><tr><td></td><td><select class="form-select" v-model="area"><option selected="">พื้นที่</option><template v-for="area in areas"><option :value="area._id">{{(area.prefix??'')+(area.name??'')}}</option></template></select></td><td> <select class="form-select" v-model="problemType"><option selected="">ปัญหา</option><option>จ่ายไม่ตรงตามแผน</option><option>ไม่จ่ายตามแผน</option><option>อื่น ๆ</option></select></td><td> <input class="form-control" v-model="problem"></td><td> <input class="form-control" v-model="solution"></td><td class="text-center"><a class="btn btn-outline-success" @click="pushToServer()">เพิ่ม</a></td></tr></thead></table></div>