<NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'รหัสบัญชี') ==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการรหัสบัญชี</h2><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info me-3" to="/portal/code/form">เพิ่มรหัสบัญชี</router-link><router-link class="btn btn-success" to="/portal/code/import">นำเข้า Excel</router-link></div><form @submit.prevent="fetchData()"><label class="form-label">ค้นหารหัสบัญชี:</label><input class="form-control" v-model="search.text" placeholder="กรุณากรอกข้อมูล"><div class="d-flex justify-content-end mt-3"><a class="btn btn-outline-success me-2" @click="search={}; fetchData();">ล้างการค้นหา</a><button class="btn btn-outline-success" type="submit">ค้นหา</button></div></form><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center"><th class="align-middle">ลำดับ</th><th class="align-middle">รหัสบัญชี</th><th class="align-middle">ชื่อบัญชี</th><th class="align-middle">หมวดค่าใช้จ่าย</th><th class="align-middle">...</th></tr></thead><tbody><tr class="text-center" v-for="(item,i) in docs"><th scope="row">{{(page*show)+(i+1)}}</th><td>{{item.code}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.category}}</td><td> <router-link :to="`/portal/code/edit/${item._id}`">แก้ไข</router-link></td></tr></tbody></table></div></div></div>