<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">ข้อมูลใบแจ้งหนี้</h2><div class="mb-3"><label class="form-label">ใบแจ้งหนี้เลขที่</label><input class="form-control" type="number" placeholder=" " v-model="doc.sequence" disabled></div><div class="mb-3"><label class="form-label">เลขที่ผู้ใช้น้ำ</label><input class="form-control" type="number" placeholder=" " v-model="doc.meter" disabled></div><div class="mb-3"><label class="form-label">ประเภทลูกค้า</label><input class="form-control" type="number" placeholder=" " v-model="doc.category" disabled></div><div class="mb-3"><label class="form-label">ชื่อ - นามสกุล</label><input class="form-control" type="text" placeholder=" " v-model="doc.name" disabled></div><div class="mb-3"><label class="form-label">ที่อยู่</label><input class="form-control" type="text" placeholder=" " v-model="doc.address" disabled></div><label class="form-label">ยอดค้างชำระ</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.debtAmount" disabled><span class="input-group-text">บาท</span></div><label class="form-label">ปริมาณการใช้น้ำ  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.qty" disabled><span class="input-group-text">ลบ.ม.</span></div><label class="form-label">ค่าบำบัดน้ำเสียตามประเภทผู้ใช้น้ำ (บาท/ลบ.ม.)  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.rate" disabled><span class="input-group-text">บาท</span></div><label class="form-label">รวมเงิน  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="total" disabled><span class="input-group-text">บาท</span></div><label class="form-label">ภาษีมูลค่าเพิ่ม  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="totalVat" disabled><span class="input-group-text">บาท</span></div><label class="form-label">ค่าบริการบำบัดน้ำเสียทีต้องชำระ  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="totalServiceVat" disabled><span class="input-group-text">บาท  </span></div><label class="form-label">รวมเงินที่ต้องชำระ  </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.invoiceAmount" disabled><span class="input-group-text">บาท</span></div><div class="row"><label class="form-label">ข้อมูลประจำเดือน:</label><div class="col-6"> <input class="form-control" type="text" placeholder=" " v-model="year" disabled></div><div class="col-6"> <input class="form-control" type="text" placeholder=" " v-model="month" disabled></div></div><div class="row mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/invoice/list?searchparam=${($route.query??{}).searchparam}`">ยกเลิก</router-link><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></form></div>