<div class="col mb-3" v-if="isDetail"><label class="form-label font-weight-bold">{{label}}</label><p v-if="new Date(modelValue).toThaiShort()!='Invalid Date'">{{new Date(modelValue).toThaiShort()}}</p></div><div class="col mb-3 position-relative" v-else><div v-if="label != undefined"><label class="input-title font-weight-bold">{{label}}</label></div><div class="input-mockup calendar form-control px-3 py-2 position-relative" @click="isActive=!isActive" :class="{'isDisabled':isDisabled}" v-on:keyup="keymonitor" tabindex="0" :disabled="isDisabled"><span v-if="strTyping.length&gt;0" style="position:absolute; top:0; right:0; background-color:#ffcff4; z-index:100; padding:0.1em 0.5em;">{{strTyping}}</span><i class="far fa-calendar-alt me-2"></i><span v-if="new Date(modelValue).toThaiShort()=='Invalid Date'">-</span><span v-else-if="moment(modelValue).isSame('0001-01-01', 'day')">-</span><span v-else>{{new Date(modelValue).toThaiShort()}}</span></div><div class="col-12 position-absolute border px-3 py-2" v-show="isActive&amp;&amp;!isDisabled" style="background-color:white;z-index:500;"><div class="row calendar-month-selector"><div class="col"><select class="form-select" v-model="selectedMonth" @change="changeMonth()"><option v-for="item in monthArr" :value="item+1">{{(item+1).intToThaiMonth()}}</option></select></div><div class="col"><select class="form-select" @input="changeYear" v-model="selectedYear"><option v-for="item in yearArr" :value="item">{{item}}</option></select></div><hr class="mt-2"></div><div class="calendar-header grid-7 text-center"><div class="col sunday">อา</div><div class="col">จ</div><div class="col">อ</div><div class="col">พ</div><div class="col">พฤ</div><div class="col">ศ</div><div class="col saturday">ส</div></div><div class="calendar-body grid-7 text-center"><li class="carlendar-day-item py-1" v-for="item in dayArr" @click="changeDate(item)" :class="{'active':moment(item).isSame(moment(modelValue), 'day'),'today':moment(item).isSame(moment(), 'day')}"> <span>{{item.getDate()}}</span></li></div></div></div>