<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='รายการผู้ใช้น้ำ')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-5">สรุปรายการผู้ใช้น้ำทั้งหมด</h2><div class="col-12 d-flex justify-content-start my-3"><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button></div><label class="d-print-none">ค้นหารายชื่อ/หมายเลขผู้ใช้น้ำ:</label><form class="d-flex d-print-none" @submit.prevent="fetchData()"><input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" v-model="search.name"><button class="btn btn-outline-success" type="submit">ค้นหา</button></form><div class="row my-2"><div class="col d-flex align-items-center mt-1 justify-content-end"><button class="btn btn-outline-success mx-2" id="clear-btn" type="submit" @click="clearSearch();">ล้างการค้นหา</button></div></div><div class="row mt-5 d-print-none"><div class="col-2 d-print-none"> <label>จำนวนที่แสดง</label><div class="col-2 d-flex justify-content-start d-print-none">  </div><select class="form-select form-select-sm mb-2" aria-label=".form-select-lg example" v-model="show" @change="fetchData(),clearPage()"> <option selected="" :value="999999999">ทั้งหมด</option><option :value="50">50</option></select></div><div class="col-2 d-print-none">   <label>ประเภทผู้ใช้น้ำ </label><select class="form-select form-select-sm mb-2" v-model="search.category" @change="fetchData()"> <option :value="undefined">ไม่ระบุ</option><option>1</option><option>2</option><option>3</option></select></div><div class="col d-flex justify-content-end mb-2"> <button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span>กำลังดึงข้อมูลจากแม่ข่าย</span></div></transition></div><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center"><th scope="col">ลำดับ</th><th scope="col">เลขที่ผู้ใช้น้ำ</th><th scope="col">ชื่อ</th><th scope="col">ที่อยู่</th><th scope="col">ประวัติ</th><th class="text-end" scope="col">ปริมาณการใช้น้ำ (ลบ.ม.)</th><th class="text-end" scope="col">ยอดชำระทั้งสิ้น (บาท)</th></tr></thead><tbody><tr v-for="(item,i) in docs"><th scope="row">{{(page*show)+(i+1)}}</th><td class="text-start"> <a class="card-link" :href="`/portal/meter/history/${item.meter}`">{{item.meter}}</a></td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.address}}</td><td> <div class="position-relative"><svg class="sparkline" :ref="`spk-${item._id}`" width="100" height="30" stroke-width="3">    </svg><span class="spktooltip" hidden="false"></span></div></td><td class="text-end">{{(item.qty??0).formatFull()}}</td><td class="text-end">{{(item.amount??0).formatFull()}}</td></tr></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>