<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='รายงานภาษีขาย')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">ภาษีขาย</h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row d-print-none"><div class="col-6"><label class="mt-3">เดือน</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.month"> <option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div><div class="col-6"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.year"> <option selected="" :value="0">กรุณาเลือก</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div></div><div class="row d-print-none"><div class="col-6"><label class="mt-3">พื้นที่</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-6"><label class="mt-3">ประเภท</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected="">ประเภท</option><option value="1,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 1</option><option value="2,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 2</option><option value="3,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 3</option><option value="1,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 1</option><option value="2,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 2</option><option value="3,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 3</option><option value="4,เหมา">ค่าบำบัดน้ำเสียประเภทเหมาจ่าย</option></select></div></div><div class="row justify-content-between mt-5 table-print"><div class="col-8"><h5>รายงานภาษีขาย (กรณีมี Vat) ประจำเดือน <span class="text-primary fw-bold">[ {{getThaiMonth(search.month)}}{{search.year}} ]</span></h5></div><div class="col-4"><h5 class="text-end">วันที่ <span class="text-primary">{{new Date(this.Date).toThaiShort()}}</span></h5></div></div><div class="row justify-content-between"><div class="col-8"><h5>ชื่อผู้ประกอบการ: <span>องค์การจัดการน้ำเสีย </span><span class="text-primary">({{(rates.find(el=>el.code==search.code)??{}).name}})</span></h5></div><div class="col-4"><h5 class="text-end fw-bold">สำนักงานใหญ่:</h5></div></div><div class="row justify-content-between"><div class="col-8"><h5>ชื่อสถานประกอบการ: <span>333 อาคารเล้าเป้งง้วน 1 ชั้น 23 ถนนวิภาวดีรังสิต แขวงจอมพล เขตจตุจักร กรุงเทพฯ 10900 02-273-8530-9</span></h5></div><div class="col-4"><h5 class="text-end fw-bold">ประเภท: <span class="text-primary">{{search.cat}}</span></h5></div></div><div class="row justify-content-between mb-5"><div class="col-8"><h5 class="fw-bold">เลขประจำตัวผู้เสียภาษี: <span class="text-primary fw-bold">0994000203021</span></h5></div><div class="col-4"><h5 class="text-end">หน่วย: [บาท]</h5></div></div><div class="row table-print"><div class="col"><span>ชำระเงินค้างรวมภาษีทั้งหมด </span><span class="text-primary">{{docs.map(el=>((el.debtAmount)*(el.vat??0)+(el.rate * el.qty))).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">ค่าบริการปัจจุบันรวมภาษีทั้งหมด </span><span class="text-primary">{{docs.map(el=>((el.totalAmount)*(el.vat??0)+(el.rate * el.qty))).reduce((a,b)=>a+b,0).formatFull()}} </span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">รวมจำนวนเงินตามใบแจ้งหนี้ทั้งหมด </span><span class="text-primary">{{(totalAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">รับเงินจริงทั้งหมด </span><span class="text-primary">{{docs.map(el=>((el.paymentAmount))).reduce((a,b)=>a+b,0).formatFull()}} </span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">ภาษีมูลค่าเพิ่มทั้งหมด </span><span class="text-primary">{{docs.map(el=>el.vat??0).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle" scope="col" rowspan="2">ลำดับ</th><th class="align-middle" scope="col" colspan="2">ใบกำกับภาษี</th><th class="align-middle" scope="col" rowspan="2">เลขที่ผู้ใช้น้ำ</th><th class="align-middle" scope="col" rowspan="2">ชื่อผู้ซื้อสินค้า/ผู้รับบริการ</th><th class="align-middle text-end" scope="col" rowspan="2">ชำระเงินค้างรวมภาษี 7% (บาท)</th><th class="align-middle text-end" scope="col" rowspan="2">ค่าบริการปัจจุบันรวมภาษี 7% (บาท)</th><th class="align-middle text-end" scope="col" rowspan="2">รวมจำนวนเงินตามใบแจ้งหนี้ (บาท)</th><th class="align-middle text-end" scope="col" rowspan="2">รับเงินจริง (บาท)</th><th class="align-middle text-end" scope="col" rowspan="2">ภาษีมูลค่าเพิ่ม</th><th class="align-middle" scope="col" rowspan="2">หมายเหตุ</th></tr><tr class="text-center"><th>วัน เดือน ปี</th><th>เลขที่</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}  </th><td>{{toThaiFormat(item.paymentDate)}}</td><td class="text-start">{{item.sequence}}</td><td class="text-start"> {{item.meter}}</td><td class="text-start"> {{item.prefix??""}} {{item.name??"-"}}</td><td class="text-end">{{((item.debtAmount)*(item.vat??0)+(item.rate*item.qty)).formatFull()}}</td><td class="text-end">{{((item.totalAmount??0)*(item.vat??0)+(item.rate*item.qty)).formatFull()}}</td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}}</td><td class="text-end">{{(item.paymentAmount).formatFull()}}</td><td class="text-end">{{(((item.invoiceAmount??0)/1.07)*0.07).formatFull()}}</td><td>{{item.description}}</td></tr></tbody></table></div></div></div>