<div id="myImage"><img class="img-fluid" src="@/assets/background.png"></div><div class="wrapper h-100 d-flex justify-content-center"><form id="formContent" @submit.prevent="getUsers" autocomplete="off"><input autocomplete="false" name="hidden" type="text" style="display:none;"><h2>ลงชื่อเข้าสู่ระบบ<span class="ms-3" v-if="userTrying">(กำลังตรวจสอบ...)</span></h2><div class="mb-3 mx-5"><input class="form-control" id="exampleInputEmail1" autocomplete="new-user" type="username" placeholder="ชื่อผู้ใช้" v-model="username"></div><div class="mb-3 mx-5"><input class="form-control" id="exampleInputPassword1" autocomplete="new-password" type="password" placeholder="รหัสผ่าน" v-model="password"></div><div class="row"><div class="col"><button class="btn btn-primary mb-3" type="submit">เข้าสู่ระบบ</button><div class="mb-2 mx-3 mb-1 mt-3"><a class="card-link" type="button" data-bs-toggle="modal" data-bs-target="#exampleModalCenter" data-bs-whatever="@mdo">ลืมรหัสผ่าน? </a></div></div></div></form></div><div class="modal fade" id="exampleModalCenter"><div class="modal-dialog modal-lg modal-dialog-centered" style="width:30em"><div class="modal-content"><div class="modal-body"><h5 class="text-center">ลืมรหัสผ่าน</h5><form><div class="mb-0"><JMInput label="อีเมล" placeholder="กรุณากรอกอีเมล" v-model="email"></JMInput></div></form><div class="d-flex justify-content-center"><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" @click="resetRequest()">รีเซ็ตรหัสผ่าน</button></div></div></div></div></div>