<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">เพิ่มข้อมูลใบแจ้งหนี้ด้วยไฟล์ Excel</h2><label>Excel</label><div class="row mb-3"></div><div class="input-group mb-3"><input class="form-control" type="file" label="Upload" @input="importExcel($event)"><button class="btn btn-outline-secondary" type="button">อัปโหลด</button></div><div class="row"><div class="col"><label class="mt-3">รหัสสายจัดเก็บ </label><label class="mt-3 text-danger"> </label><select class="form-select form-select mb-3" v-model="code"><option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div></div><div class="row"><label class="mt-3">ข้อมูลประจำเดือน:</label><div class="col-6"><select class="form-select form-select-lg mb-3" v-model="category"><option selected="">ปี</option><option>1</option><option>2</option><option>3</option><option>4</option></select></div><div class="col-6"><select class="form-select form-select-lg mb-3" v-model="categoryType"><option selected="">ปี</option><option>น้ำเสีย</option><option>น้ำทิ้ง</option><option>เหมา</option></select></div><div class="col-6"><select class="form-select form-select-lg mb-3" v-model.number="year"><option selected="">ปี</option><option>2564</option><option>2563</option><option>2562</option><option>2561</option><option>2560</option></select></div><div class="col-6">   <select class="form-select form-select mb-3" v-model.number="month"> <option selected="">เดือน</option><option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฏาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div></div><div class="row mb-3"><table class="table table-striped table-hover"><thead><tr class="table-dark"><th>#</th><th>numberInit</th><th>meter</th><th>name</th><th>address</th><th>debtText</th><th>debtAmount</th><th>qty</th><th>rate</th></tr></thead><transition-group name="fade" tag="tbody"><tr v-for="doc,i in docs" :key="doc.number"><th scope="row">{{i+1}}</th><td>{{doc.sequence}}</td><td>{{doc.meter}}</td><td>{{doc.name}}</td><td>{{doc.address}}</td><td>{{doc.debtText}}</td><td>{{doc.debtAmount}}</td><td>{{doc.qty}}</td><td>{{doc.rate}}</td></tr></transition-group></table></div><div class="row mb-5"></div><div class="d-flex justify-content-center"> <button class="btn btn-secondary mx-2" type="button" @click="$router.go(-1)">ยกเลิก</button><button class="btn btn-success mx-2" type="button" @click="postData()">บันทึก</button></div></div>