<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">จัดทำกรอบ 2</h2><div class="row pb-3"><div class="col-12"><h4 class="text-danger">**กรณีที่ต้องการทำค่าใช้จ่ายโดยใช้เฉพาะไฟล์ที่สตง.รับรองแล้วเพียงไฟล์เดียว ให้ใช้เฉพาะฝั่งซ้ายเท่านั้น<span style="text-decoration:underline;">ไม่จำเป็นต้องอัพโหลดที่ฝั่งขวา</span></h4></div></div><div class="row pb-3">   <div class="col-6"><label class="form-label">อัพโหลดไฟล์จากบัญชี</label><input class="form-control" type="file" label="Upload" @input="importLeft($event)"></div><div class="col-6"><label class="form-label">อัพโหลดไฟล์จากบัญชี (หากมีการปรับปรุงยอดเรียกชำระ)</label><input class="form-control" type="file" label="Upload" @input="importRight($event,true)"></div></div><div class="row"><div class="col-6"><div class="row"><div class="col-12 border py-3"><div class="mb-4 text-center">     <h5>รายการประมาณการค่าใช้จ่ายในการบริหารจัดการระบบบำบัดน้ำเสีย</h5></div><div class="table-container overflow-auto" style="height:600px;"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center align-middle"> <th>     </th><th>รหัสบัญชี</th><th>ชื่อบัญชี</th><th>ยอดรวม</th><th>หมวด</th></tr></thead><tbody><tr class="text-center align-middle" v-for="item,i in ledgers"><th><input type="checkbox" v-model="item.selected" disabled></th><td>{{item.code}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-end">{{(item.totalAmount??0).formatFull()}}</td><td class="text-center"><span v-if="item.category&gt;0">หมวดที่ {{item.category}}</span></td></tr></tbody></table></div></div><div class="col-12 border py-3 mt-5"><div class="mb-4 text-center">     <h5>รายการค่าใช้จ่ายในการบริหารจัดการระบบบำบัดน้ำเสียที่ สตง. รับรองแล้ว</h5></div><div class="table-container overflow-auto" style="height:600px;"><table class="table table-striped table-hover"><thead class="bg-secondary text-white"><tr class="text-center align-middle"> <th>     </th><th>รหัสบัญชี</th><th>ชื่อบัญชี</th><th>ยอดรวม</th><th>หมวด</th></tr></thead><tbody><tr class="text-center align-middle" v-for="item,i in endorsedLedgers"><th><input type="checkbox" v-model="item.selected" disabled></th><td>{{item.code}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-end">{{(item.totalAmount??0).formatFull()}}</td><td class="text-center"><span v-if="item.category&gt;0">หมวดที่ {{item.category}}</span></td></tr></tbody></table></div></div></div></div><div class="col-6"><div class="col-12 p-3 my-3 border border-secondary border border-4"><div class="mb-2 text-center">     <h5>ค่าใช้จ่ายในการบริหารจัดการระบบบำบัดน้ำเสีย</h5><h5 class="ms-2 text-primary">{{(area.prefix??'')+(area.name??'')}}</h5></div><div class="mb-4 text-center">     <span>ประจำปีงบประมาณ </span><span class="text-primary me-1">{{year}} </span><template v-if="quarter != 0"><span class="text-primary">(ไตรมาส {{quarter}})</span></template><p v-if="areaCondition.conditions[operation]!=undefined"><span>{{areaCondition.conditions[operation].category}} / </span><span>{{(areaCondition.conditions[operation].contributionLimit??0).formatFull()}} / </span><span>{{areaCondition.conditions[operation].period}} / </span><span>{{areaCondition.conditions[operation].calculation}}</span></p></div><div class="col d-flex justify-content-end mb-2"><div class="form-check"><input class="form-check-input" type="checkbox" v-model="showOnlyNonZeroes"><label class="form-check-label">แสดงเฉพาะหมวดที่มียอด </label></div></div><table class="table"><template v-for="category,i in categories"><thead><tr><th class="align-middle" scope="col" rowspan="2">{{i+1}}. {{category}}</th><th v-if="ledgers != undefined"><input class="form-control" id="newText" type="text" v-model="oldText" :class="{'form-control-plaintext':i&gt;0}"></th><th><input class="form-control" id="oldText" type="text" v-model="newText" :class="{'form-control-plaintext':i&gt;0}"></th></tr></thead><tbody><template v-for="item,j in union[i]"><tr v-if="!showOnlyNonZeroes||((item.left??{}).totalAmount&gt;0||(item.right??{}).totalAmount&gt;0)"><td>{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-end">{{((item.left??{}).totalAmount??0).formatFull()}}</td><td class="text-end">{{((item.right??{}).totalAmount??0).formatFull()}}</td></tr></template><tr><th> รวม</th><td class="text-end"> <span>{{((_.chain(union[i]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></td><td class="text-end"> <span>{{((_.chain(union[i]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></td></tr></tbody></template></table><table class="table"><thead><tr class="table-secondary"><th class="align-middle" scope="col" rowspan="3" colspan="4">รวมค่าใช้จ่ายจากการดำเนินงานขององค์การจัดการน้ำเสียสำหรับปีงบประมาณ <span class="text-primary me-1">{{year}} </span><span class="text-primary">(ไตรมาส {{quarter}})</span></th></tr></thead><tbody><tr><th> รวม</th><td class="text-end"><span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></td><td class="text-end"><span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></td></tr></tbody></table></div></div><div class="d-flex justify-content-center mt-4"><router-link class="btn btn-secondary mx-2" :to="`/portal/estimation/list?searchparam=${$route.query.searchparam}`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submitData()">บันทึก</button></div></div></div>