<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='คำขออนุมัติ')==-1"></NoAccess><div v-else><div class="modal fade" id="receiptModal"><div class="modal-dialog modal-xl"><div class="modal-content"><div class="modal-header"><div class="row mb-2"><div class="col"><span>จำนวนผู้ใช้น้ำ</span><span class="text-primary mx-2">{{(totalCount??0).formatComma()}} </span><span>คน</span><span class="ms-1"> |</span><span class="ms-1">จำนวนเงินที่รับจริง</span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">จำนวนเงินตามใบแจ้งหนี้ </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">รวมภาษีขาย </span><span class="text-primary mx-2">{{receipts.map(el=>el.vat).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">ยอดเงินคงค้าง</span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span>บาท</span></div></div></div><div class="modal-body"><div class="row"><table class="table small-text-table"><thead><tr><th>ลำดับ</th><td>เลขที่ใบแจ้งหนี้</td><td>เลขที่ผู้ใช้น้ำ</td><td>เลขที่ใบเสร็จรับเงิน</td><td>ประเภทผู้ใช้</td><td>ชื่อ-นามสกุล</td><td>ที่อยู่</td><td>ยอดเงินคงค้าง</td><td>ค่าบริการก่อนหักภาษี</td><td>ภาษีมูลค่าเพิ่ม</td><td>ยอดเงินปัจจุบัน</td><td>จำนวนเงินตามใบแจ้งหนี้</td><td>จำนวนเงินที่รับจริง</td></tr></thead><tbody> <tr v-for="item,i in receipts"><th>{{i+1}} {{item.year}} {{item.month}}</th><td>{{item.invoiceNumber}}</td><td>{{item.meter}}</td><td> <span v-if="item.sequence.search('WMA-')==-1||item.sequence.search('wma-')==-1">WMA-</span><span>{{item.sequence}}</span></td><td>{{item.category}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.address}}</td><td>{{(item.debtAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{((item.billAmount*1.07)||0).formatFull()}}</td><td>{{(item.invoiceAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{(item.paymentAmount??0).formatFull()}}</td></tr></tbody></table></div><h5 class="strong">เรียน ผอ. ฝบจ.</h5><p>กวพ.ได้จัดทำใบเสร็จรับเงินค่าบริการบำบัดน้ำเสีย <span>| ผู้ใช้น้ำประเภท 1</span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='1')??[]).length}} ใบ</span><span>| ผู้ใช้น้ำประเภท 2 </span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='2')??[]).length}} ใบ</span><span>| ผู้ใช้น้ำประเภท 3</span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='3')??[]).length}} ใบ</span><span>ประจำวันที่ ...</span><span>เป็นจำนวนเงินตามใบเสร็จ </span><span class="mx-3 text-primary">{{((totalAmount)*1.07).formatFull()}}</span><span>บาท (ดังแนบ)</span></p><p class="ms-5 mb-5">จึงเรียนมาเพื่อโปรดพิจารณา</p><p class="ms-5 mt-5">นางสาวสุภัทรา สมหวัง<p class="ms-5">ห.กวพ.</p></p></div><div class="modal-footer"><button class="btn btn-primary text-white" type="button" @click="goToRequest()">พิมพ์ใบขออนุมัติ</button><button class="btn btn-primary text-white" type="button" @click="goToForm()">preview ใบเสร็จ</button><button class="btn btn-light btn btn-secondary" type="button" data-bs-dismiss="modal" ref="cancelModal" @click="receipts=[]">ตกลง</button></div></div></div></div></div><div class="modal fade" id="signModal" tabindex="0" aria-labelledby="signModalLabel" aria-hidden="true"><div class="modal-dialog"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="signModalLabel">ลงลายมือชื่อให้ความเห็น</h5></div><div class="modal-body"><div class="mb-3"><label class="form-label">ลงลายมือชื่อยืนยัน</label><JMSign v-model="signature"></JMSign></div><div class="mb-3"><label class="mb-0">อัปโหลดลายเซ็น</label><div class="input-group mb-3"><input class="form-control" type="file" @change="chooseSignature()" ref="signatureInput"></div><div class="text-center"><img src="" width="275" alt="Image preview..." ref="signatureImage"></div></div></div><div class="modal-footer"><button class="btn btn-light btn btn-secondary" type="button" data-bs-dismiss="modal">ยกเลิก</button><button class="btn btn-success text-white btn btn-secondary" type="button" data-bs-dismiss="modal" @click="signReceipt(selected)">อนุมัติ</button></div></div></div></div><NoAccess v-if="store.state.access.findIndex(el=&gt;el=='approve')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการคำขออนุมัติ</h2><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"><th scope="col">ลำดับ</th><th scope="col">คำขอ</th><th scope="col">สถานะ</th><th scope="col">ชื่อผู้ใช้ (Username)</th><th scope="col">วันที่</th><th scope="col">ชื่อ</th><th scope="col">ข้อมูล</th><th scope="col">ข้อมูลเดิม</th><th scope="col">ข้อมูลที่แก้ไข</th><th scope="col">หมายเหตุ</th><th scope="col">เอกสารแนบ</th><th scope="col"> </th><th scope="col">รายการสถานะ</th><th scope="col"> </th></tr></thead><tbody><template v-for="item,i in docs"><tr class="text-center" v-if="item.requestType=='พิมพ์ใบเสร็จ'||item.requestType=='อนุมัติพิมพ์ใบแจ้งหนี้'"><th scope="row">{{i+1}}</th><td>{{item.requestType}}</td><td> <span class="badge rounded-pill bg-warning" v-if="item.status=='ขออนุมัติ'||item.status=='ส่งคำร้อง'">{{item.status}}</span><span class="badge rounded-pill bg-danger" v-else-if="item.status=='ไม่อนุมัติ'">{{item.status}}</span><span class="badge rounded-pill bg-info" v-else-if="item.status=='อนุมัติ'||item.status=='อนุมัติแล้วรอลงนาม'">{{item.status}}</span><span class="badge rounded-pill bg-success" v-else-if="item.status=='อนุมัติแล้วลงนามแล้ว'">{{item.status}}</span><span class="badge rounded-pill bg-muted" v-else>{{item.status}}</span></td><td> <span>{{item.username}}</span></td><td>{{new Date(item.createdAt).toThaiShort()}}</td><td>-</td><td>ใบเสร็จจำนวน: {{(item.list??[]).length}} ใบ</td><td>-</td><td>-</td><td>-</td><td>-</td><td><div class="d-flex align-items-center"><button class="btn btn-warning rounded m-2 btn-sm" type="button" data-bs-toggle="modal" data-bs-target="#receiptModal" @click="id=item._id; getReceipt(item.list)"> <i class="far fa-times-search"></i><label>ตรวจสอบ</label></button></div></td><td><div class="d-flex align-items-center" v-if="item.status=='ขออนุมัติ'"><button class="btn btn-danger rounded m-2 btn-sm" type="button" @click="updateStatus(item,'ไม่อนุมัติ')"> <i class="far fa-times-circle"></i><label>ไม่อนุมัติ</label></button></div><div class="d-flex align-items-center" v-if="item.status=='ขออนุมัติ'">  <button class="btn btn-success rounded m-2 btn-sm" type="button" @click="approveReceipt(item,'อนุมัติ')"> <i class="far fa-check-circle mx-1"> </i><label>อนุมัติ</label></button></div></td><td> <div class="d-flex align-items-center" v-if="item.status=='อนุมัติแล้วรอลงนาม'"> <a class="btn btn-warning text-white mx-2 btn-sm mt-2" type="button" data-bs-toggle="modal" data-bs-target="#signModal" @click="selected=item" v-if="item.status=='อนุมัติแล้วรอลงนาม'||item.status=='อนุมัติแล้วลงนามแล้ว'"> <i class="far fa-signature mx-1"> </i><span>ลงนาม</span></a></div></td></tr><tr class="text-center" v-else><th scope="row">{{i+1}}</th><td>{{item.requestType}}</td><th scope="row"> <span class="badge rounded-pill bg-warning" v-if="item.status=='ขออนุมัติ'||item.status=='ส่งคำร้อง'">{{item.status}}</span><span class="badge rounded-pill bg-danger" v-else-if="item.status=='ไม่อนุมัติ'">{{item.status}}</span><span class="badge rounded-pill bg-info" v-else-if="item.status=='อนุมัติแล้วรอลงนาม'">{{item.status}}</span><span class="badge rounded-pill bg-success" v-else-if="item.status=='อนุมัติ'||item.status=='อนุมัติแล้วลงนามแล้ว'">{{item.status}}</span><span class="badge rounded-pill bg-muted" v-else>{{item.status}}</span></th><td> <span v-if="item.requester!=undefined"> <span class="me-2">{{item.requester.firstName}} </span><span class="me-2">{{item.requester.lastName}}</span><span>- {{item.requester.position}}</span></span></td><td> <span v-if="item.from!=undefined">{{item.from.name}}</span><span>{{item.reason}}</span></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">ปริมาณการใช้น้ำ</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">ประเภทการคำนวณ</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">ประเภท</li></ul></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">{{item.from.qty}}</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">{{item.from.categoryType}}</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">{{item.from.category}}</li></ul></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">{{item.to.qty}}</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">{{item.to.categoryType}}</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">{{item.to.category}}</li></ul></td><td> <span>{{item.note}}</span></td><td><a :href="`${filePath}/${item.fileUrl}`">{{item.fileUrl}}</a></td><td><div class="d-flex align-items-center"><button class="btn btn-danger rounded m-2 btn-sm" type="button" @click="updateStatus(item,'ไม่อนุมัติ')"> <i class="far fa-times-circle"></i><label>ไม่อนุมัติ</label></button></div></td><td><div class="d-flex align-items-center" v-if="item.requestType=='แก้ไขใบนำส่ง'"><button class="btn btn-success rounded m-2 btn-sm" type="button" @click="editCoverLetter(item,'อนุมัติ')"> <i class="far fa-check-circle mx-1"> </i><label>อนุมัติให้แก้ไข</label></button></div><div class="d-flex align-items-center" v-else><button class="btn btn-success rounded m-2 btn-sm" type="button" @click="updateStatus(item,'อนุมัติ')"> <i class="far fa-check-circle mx-1"> </i><label>อนุมัติ</label></button></div></td><td></td></tr></template></tbody></table></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div></div>