<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4" v-if="this.$route.params.id == undefined">แบบข้อมูลต้นทุนปันส่วนน้ำ</h2><h2 class="text-decoration-underline mb-4" v-else>แก้ไขข้อมูลต้นทุนปันส่วนน้ำ</h2><div class="mb-3"><label class="form-label">พื้นที่</label><select class="form-select" v-model="doc.area"><option v-for="item in areas" :value="item._id">{{item.name}}</option></select></div><div class="mb-3"><label class="form-label">ปี</label><input class="form-control" type="number" placeholder=" " v-model.number="doc.year"></div><div class="mb-3"><label class="form-label">อัตรา</label><input class="form-control" type="number" placeholder=" " v-model.number="doc.rate"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/collection/arearation/list/`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button></div></form></div>