  setProfit(){
    this.docs.totalExpenses = 10000
    this.docs.totalIncome = 12000
    this.updateCondition()
  }
  setLoss(){
    this.docs.totalExpenses = 10000
    this.docs.totalIncome = 8000
    this.updateCondition()
