<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">เงื่อนไขตามสัญญา {{(area.prefix??'')+(area.name??'')}}</h2><div class="row"><div class="col-3"><label>จำนวนปีตามสัญญา</label><input class="form-control" type="number" v-model.number="doc.contractYear"></div><div class="col-3"><JMCalendar label="วันที่เริ่มดำเนินการ" v-model="doc.operationDate"> </JMCalendar></div><div class="col-3"><label>ปีที่เริ่มดำเนินการ</label><select class="form-select" v-model.number="doc.operationYear"><option selected="">เลือกข้อมูล</option><option v-for="item in doc.contractYear" :value="item">{{item}}</option></select></div></div><div class="row mb-5"><div class="col-3"><label>คัดลอกเงื่อนไขสมทบตั้งแต่ปีที่</label><select class="form-select form-select-sm" v-model="copySource"><option v-for="item,i in doc.conditions" :value="i">{{i+1}}</option></select></div><div class="col-3"><label>เพิ่ม...% (ถ้ามี)</label><div class="input-group-sm d-flex"><input class="form-control" type="number" v-model="addPercent"><span class="input-group-text">%</span></div></div><div class="col-3"><label>ทุกๆ...ปี (ถ้ามี)</label><div class="input-group-sm d-flex"><input class="form-control" type="number" v-model="addPeriod"><span class="input-group-text">ปี</span></div></div><div class="col-3 mt-4"><a class="btn btn-secondary btn-sm" @click="copy()">คัดลอก</a></div></div><table class="table table-stipe"><thead><tr><th>ปีที่</th><th>วันที่</th><td>เงื่อนไขสมทบ</td><td style="width: 15%">วิธีเรียกเก็บ</td></tr></thead><tbody><tr v-for="(item,i) in doc.conditions"><td> <span>{{i+1}}</span><span v-if="doc.operationYear==i+1"> <i class="fas fa-pennant text-success ms-2"></i></span></td><td> <span v-if="i+1&gt;=doc.operationYear">{{dateRange(doc.operationDate,i)}}</span></td><td><div class="input-group d-flex"><div class="form-floating flex-fill me-2"><select class="form-select" v-model="item.category" @change="item.contributionPercent=0"><option>ไม่สมทบ</option><option>สมทบตามค่าใช้จ่ายจริง/สัดส่วนเท่าเทียม</option><option>สมทบตามจำนวนคงที่</option><option>สมทบตามเปอร์เซ็นต์</option></select><label>เงื่อนไขสมทบ</label></div><template v-if="item.category=='สมทบตามค่าใช้จ่ายจริง/สัดส่วนเท่าเทียม'"><div class="form-floating me-1"><select class="form-select" v-model="item.profitType" @change="item.contributionPercent=0"><option value="1">กันไว้</option><option value="2">สัดส่วนเท่าเทียม</option></select><label>กำไร</label></div><template v-if="item.profitType == 1"><div class="form-floating" style="width: 5rem"><input class="form-control" v-model="item.contributionPercent" type="number"><label>เปอร์เซ็นต์</label></div><span class="input-group-text me-1">%</span></template><div class="form-floating me-1"><select class="form-select" v-model="item.lossType"><option value="1">ตามเงื่อนไข ที่เหลืออจน.จ่าย</option><option value="2">ตามเงื่อนไข สัดส่วนเท่าเทียม</option><option value="3">สัดส่วนเท่าเทียม</option></select><label>ขาดทุน</label></div><div class="form-floating"><JMNumber v-model="item.contributionLimit" label="จำนวน (ถ้ามี)"></JMNumber></div><span class="input-group-text me-1">บาท</span></template><template v-if="item.category=='สมทบตามจำนวนคงที่'"><JMNumber v-model="item.contributionLimit" label="จำนวน"></JMNumber><span class="input-group-text">บาท</span></template><template v-if="item.category=='สมทบตามเปอร์เซ็นต์'"><div class="form-floating" style="width: 5rem"><input class="form-control" v-model="item.contributionPercent" type="number"><label>เปอร์เซ็นต์</label></div><span class="input-group-text me-1">%</span><JMNumber v-model="item.contributionLimit" label="จำนวน (ถ้ามี)"></JMNumber><span class="input-group-text">บาท</span></template></div></td><td> <div class="form-floating"><select class="form-select" v-model="item.period"><option>รายปี</option><option>ดำเนินการก่อนจ่ายรายปี</option><option>รายไตรมาส</option><option>ดำเนินการก่อนจ่ายรายไตรมาส</option></select><label>วิธีเรียกเก็บ</label></div></td></tr></tbody></table><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/condition/list">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button></div></form></div>