<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">อัตราค่าบริการพื้นที่ {{area.name}}</h2><div class="mb-3"><label class="form-label">สายรหัสเก็บเงิน</label><input class="form-control" type="text" placeholder="nn-xx" v-model="doc.code"></div><h3 class="mb-3">ค่าบำบัดน้ำเสีย</h3><label class="form-label">หน่วยการคำนวณ</label><select class="form-select" v-model="doc.treatmentType"><option value="บาท/ลบ.ม.">บาท/ลบ.ม.</option><option value="บาท/เดือน">บาท/เดือน</option></select><div class="row mt-2"><div class="col-4"><label class="form-label">ประเภทที่ 1 : ที่อยู่อาศัยและอื่นๆ </label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.treatment1"><span class="input-group-text">{{doc.treatmentType}}</span></div></div><div class="col-4"><label class="form-label">ประเภทที่ 2 : ราชการและธุรกิจขนาดเล็ก</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.treatment2"><span class="input-group-text">{{doc.treatmentType}}</span></div></div><div class="col-4"><label class="form-label">ประเภทที่ 3 : รัฐวิสาหกิจ อุตสาหกรรมและธุรกิจขนาดใหญ่</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.treatment3"><span class="input-group-text">{{doc.treatmentType}}</span></div></div></div><h3 class="mb-3">ค่าบำบัดน้ำทิ้ง</h3><label class="form-label">หน่วยการคำนวณ</label><select class="form-select" v-model="doc.effluentType"><option value="บาท/ลบ.ม.">บาท/ลบ.ม.</option><option value="บาท/เดือน">บาท/เดือน</option></select><div class="row mt-2"><div class="col-4"><label class="form-label">ประเภทที่ 1 : ที่อยู่อาศัยและอื่นๆ </label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.effluent1"><span class="input-group-text">{{doc.effluentType}}</span></div></div><div class="col-4"><label class="form-label">ประเภทที่ 2 : ราชการและธุรกิจขนาดเล็ก</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.effluent2"><span class="input-group-text">{{doc.effluentType}}</span></div></div><div class="col-4"><label class="form-label">ประเภทที่ 3 : รัฐวิสาหกิจ อุตสาหกรรมและธุรกิจขนาดใหญ่</label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.effluent3"><span class="input-group-text">{{doc.effluentType}}</span></div></div></div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" :to="`/portal/billing/group/list/`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button></div></form></div>