<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-5">ประวัติการแก้ไข</h2><table class="table table-striped"><thead class="table-dark"><tr class="text-center"> <th>วันที่</th><th>เวลา</th><th>โดย</th><th>หมายเหตุ</th></tr></thead><tbody><tr class="text-center" v-for="item in docs"><td>{{new Date(item.createdAt).toThaiShort()}}</td><td>{{DateTime.fromISO(item.createdAt).toFormat("HH:mm")}} (v{{item.version}})</td><td>{{item.username}}</td><td> <ul><template v-for="item in resolveObj(item)"><li v-if="item!='*'">{{item}}              </li></template></ul></td></tr></tbody></table><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>