<NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'สรุปสถานะการจัดเก็บรายได้') ==-1"></NoAccess><div class="container" v-else><div class="d-flex justify-content-end my-3 d-print-none" v-if="this.$route.path != '/portal/income/dashboard'"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row table-print"><h1>สรุปผลการจัดเก็บรายได้ ปีงบประมาณ ณ {{todayFormat}}</h1><h2>สรุปสถานะการจัดเก็บรายได้ ณ {{todayFormat}}</h2><table class="table"><thead><tr class="text-center"><th> </th><th>ลำดับ</th><th>รายการ</th><th class="text-end">จำนวนเงิน<br/>(บาท)</th></tr></thead><tbody><tr class="text-center"><td><i class="fas fa-circle" style="color:#264D59;"></i></td><td>1</td><td class="text-start">ยอดลูกหนี้คงค้างก่อนปี 2564 ยกมา {{year}}</td><td class="text-end"> <span>{{docs.map(el=>((sumIncoPrev(el.incomes,[10,11,12],year-1)??0)+(sumIncoPrev(el.incomes,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0).formatFull()}}</span></td></tr><tr class="text-center"><td><i class="fas fa-circle" style="color:#43978D;"></i></td><td>2</td><td class="text-start">แผนรายได้ 2564</td><td class="text-end"><span>{{docs.map(doc=>(sumInco(doc.incomes,[10,11,12],year-1)??0)+(sumInco(doc.incomes,[1,2,3,4,5,6,7,8,9],year)??0)).reduce((a,b)=>a+b,0).formatFull()}}</span></td></tr><tr class="text-center"><td><i class="fas fa-circle" style="color:#F9E07F;"></i></td><td>3</td><td class="text-start">รวมแผนรายได้ปี 2564 (1+2)</td><td class="text-end"> <span>{{(docs.map(el=>((sumInco(el.incomes,[10,11,12],year-1)??0)+(sumInco(el.incomes,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0)+(docs.map(el=>((sumIncoPrev(el.incomes,[10,11,12],year-1)??0)+(sumIncoPrev(el.incomes,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0))).formatFull()}}</span></td></tr><tr class="text-center"><td><i class="fas fa-circle" style="color:#F9AD6A;"></i></td><td>4</td><td class="text-start">จัดเก็บได้ {{todayFormat}}</td><td class="text-end"><span>{{(docs.map(el=>((sumColc(el.collections,[10,11,12],year-1)??0)+(sumColc(el.collections,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0)+(docs.map(el=>((sumColcPrev(el.collections,[10,11,12],year-1)??0)+(sumColcPrev(el.collections,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0))).formatFull()}}</span></td></tr><tr class="text-center"><td><i class="fas fa-circle" style="color:#D46C4E;"></i></td><td>5</td><td class="text-start">รายได้ยังจัดเก็บไม่ได้ (3-4)</td><td class="text-end"><span>{{((docs.map(el=>((sumInco(el.incomes,[10,11,12],year-1)??0)+(sumInco(el.incomes,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0)+(docs.map(el=>((sumIncoPrev(el.incomes,[10,11,12],year-1)??0)+(sumIncoPrev(el.incomes,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0)))-((docs.map(el=>((sumColc(el.collections,[10,11,12],year-1)??0)+(sumColc(el.collections,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0)+(docs.map(el=>((sumColcPrev(el.collections,[10,11,12],year-1)??0)+(sumColcPrev(el.collections,[1,2,3,4,5,6,7,8,9],year)??0))).reduce((a,b)=>a+b,0))))).formatFull()}}</span></td></tr></tbody></table></div><div class="card dashboard-card mx-3 mt-5 col-12"><div class="card-body row"><canvas ref="chart1"></canvas><hr class="mt-3"><p class="card-text">แสดงข้อมูลแบบกราฟฟิค/กราฟ</p></div></div></div>