<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4 report-table-print hearder">รายละเอียดค่าใช้จ่ายเรียกเก็บเงินสมทบแบบรายไตรมาสของ {{(area.prefix??'')+(area.name??'')}}</h2><div class="row d-print-none"><div class="col-3"><JMCalendar label="วันที่เริ่มดำเนินการ" v-model="doc.operationDate"> </JMCalendar></div><div class="col-3"><JMCalendar label="วันที่สิ้นสุดสัญญา" v-model="area.contractEnd"> </JMCalendar></div><div class="col-3"><label class="form-label">ปีที่เริ่มดำเนินการ</label><p>ปีที่ {{doc.operationYear}}</p></div><div class="col-3 align-self-end text-end"><input class="form-check-input me-2" type="checkbox" v-model="showDescription"><label class="form-check-label" for="">แสดงหมายเหตุ</label></div></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><table class="table table-bordered report-table-print landscape"><thead><tr class="text-center"><th rowspan="2">ปีที่</th><th rowspan="2">ปีที่เรียกเก็บ</th><th rowspan="2">จำนวนเงินทั้งหมด</th><th colspan="3">ไตรมาสที่ ๑</th><th colspan="3">ไตรมาสที่ ๒</th><th colspan="3">ไตรมาสที่ ๓</th><th colspan="3">ไตรมาสที่ ๔</th><th rowspan="2" width="800" v-if="showDescription == true">หมายเหตุ</th></tr><tr class="text-center"><th>ต.ค.</th><th>พ.ย.</th><th>ธ.ค.</th><th>ม.ค.</th><th>ก.พ.</th><th>มี.ค.</th><th>เม.ย.</th><th>พ.ค.</th><th>มิ.ย.</th><th>ก.ค.</th><th>ส.ค.</th><th>ก.ย.</th></tr></thead><tbody><tr><td class="text-center"><span>1</span></td><td class="text-center" colspan="2"><span>อจน. จ่ายค่าซ่อมแซมเพื่อฟื้นฟู</span></td><td colspan="12"></td><td v-if="showDescription == true"> <span> </span></td></tr><template v-for="(item,i) in doc.conditions"><template v-if="i+1&gt;=doc.operationYear"><tr><td class="text-center"><span>{{i+1}}</span><span v-if="doc.operationYear==i+1"> <i class="fas fa-pennant text-success ms-2"></i></span></td><td class="text-center"><div class="row text-center"><span>ปี {{yearCollect(doc.operationDate,i).formatThai()}} </span><span class="text-muted small">({{(item.contributionLimit??0).formatFull()}} บาท)</span></div></td><td class="text-end" v-if="item.contributionLimit == undefined "><span>{{zero.formatFull().formatThai()}}</span></td><td class="text-end" v-else><span v-if="i+1&gt;=doc.operationYear">{{(calc(doc,1,i)+calc(doc,2,i)+calc(doc,3,i)+calc(doc,4,i)).formatFull().formatThai()}}</span></td><template v-for="mo,j in [10,1,4,7]"><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,j+1,i)}}</span><p>{{(calcQuarter(mo,i-(mo>9?2:1))??0).formatFull().formatThai()}}</p></td></template><td v-if="showDescription == true"> <span class="text-decoration-underline">วิธีการคิด</span><p>{{getHowToCalc(i)}}</p></td></tr></template></template><tr><td class="text-center"><span>***</span></td><td class="text-center"><span v-if="doc.operationDate != undefined">ปี {{yearCollect(doc.operationDate,(doc.conditions??0).length).formatThai()}}</span><span v-else>ปี</span></td><td class="text-end"><span>{{((calcLast(1)??0)+(calcLast(2)??0)+(calcLast(3)??0)+(calcLast(4)??0)).formatFull().formatThai()}}</span></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,1,(doc.conditions??0).length)}}</span><p>{{(calcLast(1)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,2,(doc.conditions??0).length)}}</span><p>{{(calcLast(2)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,3,(doc.conditions??0).length)}}</span><p>{{(calcLast(3)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,4,(doc.conditions??0).length)}}</span><p>{{(calcLast(4)??0).formatFull().formatThai()}}</p></td><td v-if="showDescription == true"><span>สิ้นสุดสัญญาวันที่ <span v-if="this.area.contractEnd!=undefined">{{new Date(this.area.contractEnd).toThaiShort().formatThai()}}</span><span v-else>-</span></span></td></tr></tbody></table></form></div>