<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">เพิ่มข้อมูลการชำระเงินด้วยไฟล์ Excel</h2><label class="mb-0">Import Excel</label><div class="input-group mb-3"><input class="form-control" type="file" label="Upload" @input="importExcel($event)"></div><form class="row" @submit.prevent="postData()"><div class="row mb-3"><table class="table table-striped table-hover"><thead><tr class="table-dark"><th scope="col">ลำดับ</th><th scope="col">วันที่ชำระ</th><th scope="col">เลขที่ผู้ใช้น้ำ</th><th scope="col">ชื่อ</th><th scope="col">ที่อยู่</th><th class="text-end" scope="col">จำนวนเงินที่รับจริง</th></tr></thead><transition-group name="fade" tag="tbody"><tr v-for="doc,i in docs" :key="doc.number"><th scope="row">{{i+1}}</th><td>{{new Date(doc.paymentDate).toThaiShort()}}</td><td>{{doc.meter}}</td><td>{{doc.name}}</td><td>{{doc.address}}</td><td class="text-end">{{(doc.paymentAmount??0).formatFull()}}</td></tr></transition-group></table></div></form><div class="row mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/payment/list/`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="postData()">บันทึก</button></div></div>