<div class="container-fluid p-4"><!--เช็ค debug--><div class="row justify-content-center"><div class="col-10 p-2 border border-4 border-light my-3"><div class="mb-4 text-center">     <span>ประจำปีงบประมาณ </span><span class="text-primary me-1">{{year}} </span><span class="text-primary">(ไตรมาส {{quarter}})</span></div><div class="mb-4"><div class="row"><p><span class="me-2">{{condition.category}}</span><span class="me-2">{{condition.calculation}}</span><span class="me-2">{{condition.period}}</span><span class="me-2" v-if="condition.contributionPercent&gt;0">คิดจากเปอร์เซ็นต์ค่าใช้จ่าย {{(condition.contributionPercent??0).formatFull()}} %</span><span class="me-2" v-if="condition.contributionLimit&gt;0"> <span v-if="condition.category!='สมทบตามจำนวนคงที่'">แต่ไม่เกิน </span><span v-if="quarter&gt;0">{{((condition.contributionLimit??0)).formatFull()}}บาท/ปี = {{((condition.contributionLimit??0)/4).formatFull()}} บาท/ไตรมาส</span><span v-else>{{((condition.contributionLimit??0)).formatFull()}}</span></span></p><div class="d-flex" style="font-size:0.5rem;"><p class="text-muted" v-if="condition.profitType!=undefined"><span class="me-2">หากกำไร {{condition.profitType}}</span></p><p class="text-muted" v-if="condition.lossType!=undefined"><span class="me-2">หากขาดทุน {{condition.lossType}}</span></p></div></div></div></div><div class="mb-2 text-center">     <h5>รายการคำนวณยอดเงินสมทบที่ อจน. เรียกเก็บจาก</h5><h5 class="ms-2 text-primary">{{(area.prefix??'')+(area.name??'')}}</h5></div><textarea class="form-control" v-model="doc.contributionText"> </textarea><table class="table my-3"><tbody><tr v-if="isEndorsed"> <td></td><td> </td><td class="text-center">{{doc.oldText}}</td><td class="text-center">{{doc.newText}}</td><td></td></tr><tr v-if="(category??[]).name == 'สมทบตามจำนวนเปอร์เซนต์'"><td> </td><td>คิดค่าใช้จ่ายร้อยละ {{condition.contributionPercent}}</td><td> </td><td> </td><td v-if="isEndorsed">  </td></tr><template v-for="item,index in (category??[]).children"><tr v-if="(item.search('เงื่อนไขสมทบค่าใช้จ่าย/ในสัดส่วนเท่าเทียมกันไม่เกิน')==-1&amp;&amp;area.name!='ลำปาง')||area.name=='ลำปาง'"><td></td><td>{{item.replace("*area*",(area.prefix??'')+(area.name??'')).replace("*year*",year).replace("*quarter*",quarter).replace("*contributionLimit*",(condition.contributionLimit??0).formatFull()).replace("*contributionPercent*",condition.contributionPercent).replace('ไตรมาสที 0',"")}}</td><td class="text-end"> <template v-if="item.search('คืนค่าใช้จ่ายที่')!=-1"><JMNumber label="คืนค่าใช้จ่าย" v-model="doc.advanceAmount" @change="categoryCalculation()"> </JMNumber></template><template v-else-if="item.search('คิดค่าใช้จ่ายร้อยละ')!=-1"><span> </span></template><template v-else><template v-if="(Math.abs(calculation1[index]??0)).formatFull()==='NaN'"><span>0 </span></template><template v-else><span>{{(calculation1[index]??0).formatFull()}}  </span></template></template></td><td class="text-end" v-if="isEndorsed"> <template v-if="item.search('คืนค่าใช้จ่ายที่')!=-1"><JMNumber label="คืนค่าใช้จ่าย" v-model="doc.eAdvanceAmount" @change="categoryCalculation()"> </JMNumber></template><template v-else-if="item.search('คิดค่าใช้จ่ายร้อยละ')!=-1"><span> </span></template><template v-else><template v-if="(Math.abs(calculation2[index]??0)).formatFull()==='NaN'"><span>0 </span></template><template v-else><span>{{(calculation2[index]??0).formatFull()}} </span></template></template></td></tr><td> </td></template></tbody></table><table class="table my-3"><tbody><tr><td colspan="2"><span class="text-warning small">เพิ่มรายการปรับปรุง (ถ้ามี)</span></td><td :colspan="isEndorsed?'2':'1'"><div class="d-flex justify-content-end"><a class="btn btn-success text-white" @click="pushItem({name:'',symbol:'+',amount:0,eAmount:0})"><i class="fas fa-plus-circle me-2"></i><span>เพิ่มรายการ</span></a></div></td><td></td></tr><tr v-for="(item,i) in doc.modifications"><td class="align-middle"><input class="form-control" v-model="item.name" style="direction: ltr;"></td><td class="align-middle"><select class="form-select" v-model="item.symbol" @change="checkModified()"><option>-</option><option>+</option></select></td><td class="align-middle"><JMNumber v-model.number="item.amount" @change="checkModified()" label="ยอดเงิน"></JMNumber></td><td class="align-middle" v-if="isEndorsed"><JMNumber v-model.number="item.eAmount" @change="checkModified()" label="ยอดเงิน"></JMNumber></td><td><a class="btn btn-danger mx-2" @click="doc.modifications.splice(i,1); categoryCalculation()">  <i class="fas fa-trash-alt">        </i></a></td></tr><tr><td class="align-middle"><div v-if="doc.totalExpenses&gt;doc.totalIncome"> <div v-if="quarter == 0"><td class="col-7"> <span>ยอดที่ อปท. พึงชำระสำหรับปีงบประมาณ</span></td><td><input class="form-control" v-model.number="doc.collectYear"></td></div><div v-else><td class="col-4"> <span>ยอดที่ อปท. พึงชำระสำหรับปีงบประมาณ</span></td><td class="col-4"><input class="form-control" v-model.number="doc.collectYear"></td><td class="col-4">ไตรมาสที่ {{quarter}}</td></div></div><div v-else> <div v-if="quarter == 0"><td class="col-7"> <span>รวมที่พึงชำระสำหรับปีงบประมาณ</span></td><td><input class="form-control" v-model.number="doc.collectYear"></td></div><div v-else><td class="col-4"> <span>รวมที่พึงชำระสำหรับปีงบประมาณ</span></td><td class="col-4"> <input class="form-control" v-model.number="doc.collectYear"></td><td class="col-4"> ไตรมาสที่ {{quarter}}</td></div></div></td><td class="text-end"> </td><td class="text-end align-middle" style="border-bottom:double 5px black;"> <JMNumber v-model.number="doc.contributionAmount" label="ยอดเงิน"></JMNumber></td><td class="text-end align-middle" v-if="isEndorsed" style="border-bottom:double 5px black;"><JMNumber v-model.number="doc.eContributionAmount" label="ยอดเงิน"></JMNumber></td></tr></tbody></table><div class="d-flex justify-content-center mt-4"><router-link class="btn btn-secondary mx-2" :to="`/portal/summary/list?searchparam=${$route.query.searchparam}`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submitData()">บันทึก</button></div></div></div>