<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">เพิ่มอปท.</h2><div class="row"><div class="col-6 mb-3"><label class="form-label">เลขที่สัญญา</label><input class="form-control" type="text" placeholder=" " v-model="doc.contractNumber"></div><div class="col-1 mb-3"><label class="form-label">ชื่อเรียกพื้นที่</label><select class="form-select" type="text" placeholder=" " v-model="doc.prefix"><option>ทม.</option><option>ทต.</option><option>ทน.</option><option>อบต.</option><option>อบจ.</option></select></div><div class="col-5 mb-3"><label class="form-label">ชื่อเรียกพื้นที่</label><input class="form-control" type="text" placeholder=" " v-model="doc.name"></div><div class="col-6 mb-3"><label class="form-label">รหัสพื้นที่</label><input class="form-control" type="text" placeholder="รหัสพื้นที่" v-model="doc.code"></div><div class="col-12"><div class="form-check form-switch"><input class="form-check-input" id="reportIncome" type="checkbox" v-model="doc.reportIncome"><label class="form-check-label" for="reportIncome">แสดงในรายงานจัดเก็บ (เขียวเหลือง)</label></div></div><hr class="rounded my-5"><h3 class="mb-3">ที่อยู่อปท.</h3><div class="row px-4"><div class="col-12 mb-3"> <label class="form-label">ที่อยู่</label><textarea class="form-control" v-model="doc.address" line="5"></textarea></div><div class="col-6 mb-3"><label class="form-label">จังหวัด</label><select class="form-select" v-model="doc.province"><option selected="">เลือกข้อมูล</option><option v-for="province in thailand">{{province.name.th}}</option></select></div><div class="col-6 mb-3"><label class="form-label">เขต/อำเภอ</label><select class="form-select" v-model="doc.district" @change="updateTambon"><option selected="">เลือกข้อมูล</option><option v-for="amphoe in districts">{{amphoe.name.th}}</option></select></div><div class="col-6 mb-3" :key="doc.district"><label class="form-label">แขวง/ตำบล</label><select class="form-select" id="tambon-select" v-model="doc.subDistrict" @change="updatePostal()"><option value="">อื่นๆ</option><option v-for="tambon in subDistricts">{{tambon.name.th}}</option></select></div><div class="col-6 mb-3"><label class="form-label">รหัสไปรษณีย์</label><input class="form-control" type="text" placeholder=" " v-model="doc.postal"></div></div><div class="mb-3"></div><hr class="rounded my-4"><h3 class="mb-3">เอกสารสัญญา</h3><div class="row"><div class="col-4"><JMCalendar label="วันที่เริ่มสัญญา" v-model="doc.contractStart"> </JMCalendar></div><div class="col-4"><JMCalendar label="วันที่สิ้นสุดสัญญา" v-model="doc.contractEnd"> </JMCalendar></div><div class="col-4"><JMCalendar label="วันที่เซ็นต์สัญญา" v-model="doc.contractSigned"></JMCalendar></div></div><div class="row"><label class="form-label">MOA/MOU/สัญญาก่อสร้าง/ที่ปรึกษา (รายละเอียดสัญญา) - จ้างที่ปรึกษารับออกแบบระบบบำบัดน้ำเสีย</label><div class="col-12"><div class="row"><div class="col-md-6"><JMUploads v-model="doc.fileUrl1" folder="areacontract"></JMUploads></div><div class="col-md-6"><ul class="list-group"><li class="list-group-item" v-for="item in doc.fileUrl1"> <div class="d-flex justify-content-between"><span>{{item}}</span><a class="btn btn-primary d-flex justify-content-center align-items-center text-white" :href="`${filePath}/${item}`"><i class="fas fa-cloud"></i></a><a class="btn btn-danger d-flex justify-content-center align-items-center" @click="doc.fileUrl1.splice(i,1)"><i class="fas fa-trash-alt"></i></a></div></li></ul></div></div></div></div><div class="row"><label class="form-label">บันทึกแนบท้ายเอกสารสัญญา</label><div class="col-12"><div class="row"><div class="col-md-6"><JMUploads v-model="doc.fileUrl2" folder="areaaddendum"></JMUploads></div><div class="col-md-6"><ul class="list-group"><li class="list-group-item" v-for="item in doc.fileUrl2"> <div class="d-flex justify-content-between"><span>{{item}}</span><a class="btn btn-primary d-flex justify-content-center align-items-center text-white" :href="`${filePath}/${item}`"><i class="fas fa-cloud"></i></a><a class="btn btn-danger d-flex justify-content-center align-items-center" @click="doc.fileUrl2.splice(i,1)"><i class="fas fa-trash-alt"></i></a></div></li></ul></div></div></div></div></div><div class="row mb-3"><div class="col-6"><label class="form-label">จำนวนปีตามสัญญา</label><input class="form-control" type="number" v-model.number="doc.contractYear"></div></div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/area/list">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></form></div>