<NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'สรุปรายรับ - รายจ่าย (กรอบ 3)') ==-1"></NoAccess><div class="container-fluid p-4" v-else><StepIncome step="2"></StepIncome><h2 class="text-decoration-underline mb-4">รายการประมาณการสรุปรายรับ - จ่ายในการบริหารจัดการระบบบำบัดน้ำเสีย</h2><h5 class="text-muted">(รายการกรอบ 3)</h5><div class="col-12 d-flex justify-content-end"> </div><JMIncomeStepSearch v-model="search" @clear="search={}; fetchData();" @search="fetchData();"></JMIncomeStepSearch><div class="row mt-5"><label>จำนวนที่แสดง</label><div class="col-2 d-flex justify-content-start">  <select class="form-select form-select-sm mb-2" v-model.number="show" @change="fetchData()"><option selected="" value="9999999">ทั้งหมด</option><option value="50">50</option></select></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center align-middle"><th scope="col" rowspan="2">ลำดับ</th><th scope="col" rowspan="2">ประเภท</th><th scope="col" rowspan="2">พื้นที่</th><th scope="col" rowspan="2">ปีงบประมาณ</th><th scope="col" rowspan="2">...</th><th scope="col" colspan="2">ข้อมูลกรอบ 2</th><th scope="col" rowspan="2">กรอบ 3</th></tr><tr class="text-center"><th>Updated</th><th>...</th></tr></thead><tbody><tr class="text-center align-middle" v-for="item,i in docs"><th scope="row"> <div class="row"><span>{{(page*show)+(i+1)}}  </span><i class="fas fa-check-circle text-success" v-if="item.isKrob3"></i></div></th><td>{{item.prefix??""}}</td><td>{{item.name??"-"}}</td><td>{{item.year}}</td><td> <span v-if="item.quarter!=0">{{item.quarter}}</span><span v-else>รายปี</span></td><td> <span v-if="(item.calculations[item.calculations.length-1]??{}).createdAt!=undefined">{{new Date((item.calculations[item.calculations.length-1]??{}).createdAt).toThaiShort()}}</span><span v-else>-</span></td><td> <router-link class="btn btn-info" v-if="(item.calculations[item.calculations.length-1]??{}).createdAt!=undefined" :to="`/portal/calculation/form/${item.area}/${item.year}/${item.j}/${item.quarter}?searchparam=${JSON.stringify(this.search)}`">จัดทำกรอบ 3</router-link></td><td><router-link class="btn btn-danger" v-if="(item.calculations[item.calculations.length-1]??{}).createdAt!=undefined" :to="`/portal/calculation/pdf/${item.area}/${item.year}/${item.j}/${item.quarter}`" target="_blank">PDF</router-link></td></tr></tbody></table></div></div><nav><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>