<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">แผนการจัดเก็บ {{(area.prefix??"")+(area.name??"")}}</h2><table class="table table-borderless"><thead><tr style="border-bottom:2px solid black;"><th>#</th><td>จำนวนเงิน</td><td>ปีงบประมาณ</td><td>เดือน</td><td width="250">ประเภทแผน</td><td> <i class="fas fa-ellipsis-h"></i></td></tr></thead><tbody><template v-for="item,i in docs"><tr><th>{{i+1}}</th><td> <JMNumberLite v-model="item.amount" suffix="บาท"></JMNumberLite></td><td> <select class="form-select form-select-xs mb-3" :id="`select-year-${item._id}`" v-model.number="item.year"> <option selected="" :value="0">กรุณาเลือก</option><template v-for="item,i in 24"><option>{{(new Date().getFullYear()+543)-i + 6}}</option></template></select></td><td> <select class="form-select form-select-xs mb-3" aria-label=".form-select-lg example" v-model.number="item.month"> <option selected="" :value="13">กรุณาเลือก</option><option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></td><td> <select class="form-select" v-model.number="item.isDebt"><option :value="undefined">ยังไม่ได้ระบุ (แผนเงินรายรับตามปี)</option><option :value="true">แผนเงินค้าง</option><option :value="false">แผนเงินรายรับตามปี</option></select></td><td><a class="btn btn-danger" @click="deleteData(item._id)"><i class="fas fa-trash-alt"></i></a></td></tr><tr><td colspan="12"><div class="form-group"><label for="exampleFormControlTextarea1">หมายเหตุ</label><textarea class="form-control" id="exampleFormControlTextarea1" v-model="item.remark1"></textarea></div></td></tr><tr style="border-bottom:1px solid grey;"><td colspan="3"><JMUploads v-model="item.fileUrl" folder="areaplan"></JMUploads></td><td colspan="4"><ul class="list-group"><li class="list-group-item" v-for="(el,k) in item.fileUrl"> <div class="d-flex justify-content-between"><span>{{el}}</span><a :href="`${filePath}/${el}`"><i class="fas fa-cloud me-3"></i><span>ดาวน์โหลด</span></a><a class="btn btn-danger" @click="item.fileUrl.splice(k,1)"><i class="fas fa-trash-alt"></i></a></div></li></ul></td></tr></template></tbody></table><div class="d-flex justify-content-center mt-3 mb-5"><button class="btn btn-success text-white mx-2" type="button" @click="addItem()"><i class="fas fa-plus-circle me-2"></i><span>เพิ่มรายการ</span></button></div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/area/plan/list">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="updateItems()">บันทึก</button></div></form></div>