<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='user')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการผู้ใช้งาน</h2><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info mx-3" to="/portal/user/form">เพิ่มผู้ใช้      </router-link></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"><th>ชื่อผู้ใช้</th><th>ชื่อ-นามสกุล</th><th>ตำแหน่ง</th><th>สิทธิ์การใช้งาน</th><th>...</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><td>{{item.username}}</td><td>{{item.firstName}} {{item.lastName}}</td><td>{{item.position}}</td><td>{{item.accessLevel}}</td><td> <router-link class="btn btn-warning" :to="`/portal/user/edit/${item._id}`">แก้ไข</router-link></td></tr></tbody></table></div></div></div>