<NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'เงื่อนไขสัญญา อปท.') ==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการเงื่อนไขตามสัญญา อปท.</h2><JMIncomeSearch class="mb-5" @search="fetchData()" v-model="search"></JMIncomeSearch><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center"><th class="align-middle">ลำดับ</th><th class="align-middle">พื้นที่</th><th class="align-middle">ประมาณการรายรับ</th><th class="align-middle">แก้ไข</th></tr></thead><tbody><tr class="text-center" v-for="(item,i) in docs"><th scope="row"> {{(page*show)+(i+1)}}</th><td>{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-center"><router-link class="btn btn-success text-white" v-if="item.isFixed &amp;&amp; item.operationDate!=undefined" :to="`/portal/condition/detail/${item._id}`"><span>รายละเอียด</span></router-link></td><td class="text-center"><router-link class="btn btn-warning" :to="`/portal/condition/edit/${item._id}`"><span>แก้ไข</span></router-link></td></tr></tbody></table></div></div><nav><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>