<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='สายรหัสเก็บเงิน')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">สายรหัสเก็บเงิน / อัตราค่าบริการ</h2><JMIncomeSearch @search="fetchData()" v-model="search"></JMIncomeSearch><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"> <th rowspan="2">ลำดับ</th><th rowspan="2">พื้นที่</th><th rowspan="2">สายรหัสเก็บเงิน</th><th colspan="3">ค่าบำบัดน้ำเสีย</th><th colspan="3">ค่าบำบัดน้ำทิ้ง</th><th rowspan="2">แก้ไข</th></tr><tr class="table-dark text-center"><td class="text-end">ประเภทที่ 1</td><td class="text-end">ประเภทที่ 2</td><td class="text-end">ประเภทที่ 3</td><td class="text-end">ประเภทที่ 1</td><td class="text-end">ประเภทที่ 2</td><td class="text-end">ประเภทที่ 3</td></tr></thead><tbody class="align-middle"><template v-for="item,i in docs"><tr class="text-center" v-show="i&gt;=page*show &amp;&amp; i&lt;(page*show) + show"><td>{{i+1}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td> <span :class="{'text-muted':(item.code=='กรุณาเพิ่มข้อมูล')}">{{item.code}}</span></td><td class="text-end"> <span :class="{'text-muted':(item.treatment1=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.treatment1=='number'">{{(item.treatment1).formatFull()}} {{item.treatmentType}}</span><span v-else>{{item.treatment1}} </span></span></td><td class="text-end"> <span :class="{'text-muted':(item.treatment2=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.treatment2=='number'">{{(item.treatment2).formatFull()}} {{item.treatmentType}}</span><span v-else>{{item.treatment2}}</span></span></td><td class="text-end"> <span :class="{'text-muted':(item.treatment3=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.treatment3=='number'">{{(item.treatment3).formatFull()}} {{item.treatmentType}}</span><span v-else>{{item.treatment3}}</span></span></td><td class="text-end"> <span :class="{'text-muted':(item.effluent1=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.effluent1=='number'">{{(item.effluent1).formatFull()}} {{item.effluentType}}</span><span v-else>{{item.effluent1}}</span></span></td><td class="text-end"> <span :class="{'text-muted':(item.effluent2=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.effluent2=='number'">{{(item.effluent2).formatFull()}} {{item.effluentType}}</span><span v-else>{{item.effluent2}}</span></span></td><td class="text-end"> <span :class="{'text-muted':(item.effluent3=='กรุณาเพิ่มข้อมูล')}"> <span v-if="typeof item.effluent3=='number'">{{(item.effluent3).formatFull()}} {{item.effluentType}}</span><span v-else>{{item.effluent3}}</span></span></td><td> <router-link class="btn btn-warning" :to="`/portal/billing/group/${item.path}`">แก้ไข </router-link></td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>