<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='รายงานตัดยอดหนี้')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">ตัดยอดหนี้</h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row d-print-none"><div class="col-6"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.year"> <option selected="" :value="0">กรุณาเลือก</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6"><label class="mt-3">เดือน</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.month"> <option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div></div><div class="row d-print-none"><div class="col-6"><label class="mt-3">พื้นที่</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-6"><label class="mt-3">ประเภท</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected="">ประเภท</option><option value="1,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 1</option><option value="2,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 2</option><option value="3,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 3</option><option value="1,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 1</option><option value="2,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 2</option><option value="3,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 3</option><option value="4,เหมา">ค่าบำบัดน้ำเสียประเภทเหมาจ่าย</option></select></div></div><div class="row d-flex align-items-start mt-5 table-print"><div class="col-sm-12 col-md-6"><div class="row"><h5>องค์การจัดการน้ำเสีย <span class="text-primary">({{(rates.find(el=>el.code==search.code)??{}).name}})</span></h5></div><div class="row"><h5>ประจำวันที่ <span class="text-primary fw-bold">{{new Date(this.Date).toThaiShort()}}</span></h5></div><div class="row"><h5>ประจำเดือน <span class="text-primary"> {{getThaiMonth(search.month)}} {{search.year}}</span></h5></div></div><div class="col-sm-12 col-md-6"><div class="row"><h5 class="text-end">333 อาคารเล้าเป้งง้วน 1 ชั้น 23 ถนนวิภาวดีรังสิต แขวงจอมพล เขตจตุจักร กรุงเทพฯ 10900</h5></div><div class="row"><h5 class="text-end">หน่วย: [บาท]</h5></div></div></div><div class="row"><div class="col"><span>จำนวนเงินที่รับจริง</span><span class="text-primary">{{(totalPayment??0).formatFull()}} </span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินตามใบแจ้งหนี้ </span><span class="text-primary">{{inclVat(totalInvoice??0).formatFull()}} </span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินตามใบแจ้งหนี้รวมยอดค้างชำระ</span><span class="text-primary">{{inclVat((totalInvoice??0)+(totalDebt??0)).formatFull()}} </span><span>บาท</span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle">ลำดับ</th><th class="align-middle">เลขที่ผู้ใช้น้ำ</th><th class="align-middle">ชื่อ-นามสกุล</th><th class="align-middle">เลขที่ใบแจ้งหนี้</th><th class="align-middle">ประเภท</th><th class="align-middle">วันที่รับเงิน</th><th class="align-middle">ประจำเดือน</th><th class="align-middle text-end">จำนวนเงินตามใบแจ้งหนี้ (บาท)</th><th class="align-middle text-end">จำนวนเงินที่รับจริง (บาท)</th><th class="align-middle text-end">ตัดยอดหนี้</th><th class="align-middle text-end">ยอดค้างชำระ (บาท)</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}</th><td>{{item.meter}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.invoiceNumber}}</td><td>{{item.category}}</td><td>{{toThaiFormat(item.paymentDate)}}</td><td> <template v-if="item.month!=undefined&amp;&amp;item.year!=undefined"><span>{{getThaiMonth(item.month)}}{{(item.year).toString().substring(2, 4)}}</span></template></td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}}</td><td class="text-end">{{(item.paymentAmount??0).formatFull()}}</td><td class="text-end"> <span>{{item.debtText}} <br/></span><span>{{inclVat(item.debtAmount,item.vatRate)}} บาท</span></td><td class="text-end"> <span>{{inclVat((item.billAmount??0)+(item.debtAmount??0),item.vatRate).formatFull()}}</span></td></tr></tbody></table></div></div></div>