m-label" for="">สรุปรายรับ - รายจ่าย (กรอบ 3)</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="คำนวณยอดเงินสมทบ (กรอบ 4)" v-model="doc.access"><label class="form-label" for="">คำนวณยอดเงินสมทบ (กรอบ 4)</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="ใบนำส่ง/Invoice" v-model="doc.access"><label class="form-label" for="">ใบนำส่ง/Invoice</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="แผนการจัดเก็บรายได้ประจำปีงบประมาณ" v-model="doc.access"><label class="form-label" for="">แผนการจัดเก็บรายได้ประจำปีงบประมาณ</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="สถิติผลการจัดเก็บรายได้ที่ผ่านมา" v-model="doc.access"><label class="form-label" for="">สถิติผลการจัดเก็บรายได้ที่ผ่านมา</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="สรุปสถานะการจัดเก็บรายได้" v-model="doc.access"><label class="form-label" for="">สรุปสถานะการจัดเก็บรายได้</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="เปรียบเทียบแผน/ผลรายเดือน" v-model="doc.access"><label class="form-label" for="">เปรียบเทียบแผน/ผลรายเดือน</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="ตารางแผน/ผลการจัดเก็บรายได้ที่ผ่านมา" v-model="doc.access"><label class="form-label" for="">ตารางแผน/ผลการจัดเก็บรายได้ที่ผ่านมา</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="เงื่อนไขสัญญา อปท." v-model="doc.access"><label class="form-label" for="">เงื่อนไขสัญญา อปท.</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="บันทึกการรับเงิน อปท." v-model="doc.access"><label class="form-label" for="">บันทึกการรับเงิน อปท.</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="รหัสบัญชี" v-model="doc.access"><label class="form-label" for="">รหัสบัญชี</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="ต้นทุนปันส่วนน้ำ" v-model="doc.access"><label class="form-label" for="">ต้นทุนปันส่วนน้ำ</label></div><div class="form-check"><input class="form-checkbox me-2" id="" type="checkbox" value="แผนการจัดเก็บรายได้" v-model="doc.access"><label class="form-label" for="">แผนการจัดเก็บรายได้</label></div></template></div><div class="col mt-2"><input class="form-checkbox me-2" id="access03" type="checkbox" v-model="doc.access" value="area"><label for="access03">ระบบพื้นที่ อปท.</label></div><div class="col mt-2"><input class="form-checkbox me-2" id="access04" type="checkbox" v-model="doc.access" value="user"><label for="access04">ระบบจัดการผู้ใช้</label></div><div class="col mt-2"><input class="form-checkbox me-2" id="access05" type="checkbox" v-model="doc.access" value="approve"><label for="access05">คำขออนุมัติ</label></div><div class="col mt-2"><input class="form-checkbox me-2" id="access06" type="checkbox" value="dashboard billing" v-model="doc.access"><label class="form-label" for="access06">Dashboard Billing</label></div><div class="col mt-2"><input class="form-checkbox me-2" id="access07" type="checkbox" value="dashboard จัดเก็บรายได้" v-model="doc.access"><label class="form-label" for="access07">Dashboard จัดเก็บรายได้</label></div></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/user/list`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submit()">บันทึก</button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></form></div>