
import address from "@/variable";
import axios from "axios";
import { Options, Vue } from "vue-class-component";
import { DateTime } from "luxon";

@Options({
  components: {},
  props: {},
})
export default class ReceiptList extends Vue {
  docs: Array<any> = [];
  invoices: Array<any> = [];
  receipts: Array<any> = [];
  search: any = {
  };
  DateTime = DateTime;

  get filtered(){
    let result = this.docs
    if(this.search.year != undefined) result = result.filter(el=>{return el.year == this.search.year})
    if(this.search.month != undefined) result = result.filter(el=>{return el.month == this.search.month})
    if(this.search.isPaid == true) result = result.filter(el=>{return this.findReceipt(el)!=undefined})
    if(this.search.isPaid == false) result = result.filter(el=>{return this.findReceipt(el)==undefined})
    if(this.search.text != "" && this.search.text != undefined) result = result.filter(el=>{return el.sequence.search(this.search.text) != -1})
    // result = result.sort((a:any,b:any)=>(parseInt(a.sequence)??0)-(parseInt(b.sequence)??0))
    return result
  }

  toThaiFormat(year: number, month: number) {
    var dtThai = DateTime.fromObject({ year: year - 543, month })
      .reconfigure({ outputCalendar: "buddhist" })
      .setLocale("th")
      .toFormat("LLLyy");
    return dtThai;
  }
  findReceipt(item:any){
    console.log(item.year,this.receipts)
    return this.receipts.find(el=>(el.year==item.year&&el.month==item.month))
  }
  mounted() {
    this.fetchData();
  }
  fetchData() {
    let query = {
      search: {meter: this.$route.params.id},
      limit: 999999,
      skip: 0,
      sort: {
        sequence: -1,
      }
    };
    axios.post(`${address}/invoices-paginate`, query).then((response) => {
      this.docs = response.data.docs;
      let query = {
        search: { meter: this.$route.params.id },
        limit: 999999,
        skip: 0,
      };
      axios.post(`${address}/receipts-paginate`, query).then((receipts) => {
        this.receipts = receipts.data.docs;
      });
    });
  }
  downloadExcel(){
    console.log("download excel...");
    let self = this;
    const Excel = require("exceljs")
    var workbook = new Excel.Workbook();
    var worksheet = workbook.addWorksheet(`ประวัติผู้ใช้น้ำ`);
    let header = [
      'ลำดับ',
      'หมายเลขใบแจ้งหนี้',
      'ประจำเดือน',
      'ปริมาณการใช้น้ำ (ลบ.ม.)',
      'อัตราค่าบำบัด',
      'รวมเงิน',
      'ภาษี (บาท)',
      'ค่าบริการบำบัดน้ำเสีย',
      'เดือนชำระ',
      'หมายเลขใบเสร็จ',
      'ชำระทั้งสิ้น',
      'จำนวนเงินที่รับจริง',
    ]
    let headerRow = worksheet.addRow(header);
    ['A','B','C','D','E','F','G','H','I','J','K','L'].map(key => {
        worksheet.getColumn(key).width = 25
    });
    this.filtered.forEach((item, i) => {
      worksheet.addRow([
        i+1,
        item.sequence,
        this.toThaiFormat(item.year,item.month),
        (item.qty??0).formatFull(),
        (item.rate??0).formatFull(),
        ((item.qty??0)*(item.rate??0)).formatFull(),
        (((item.qty??0)*(item.rate??0))*0.07).formatFull(),
        (((item.qty??0)*(item.rate??0))*1.07).formatFull(),
        (this.findReceipt(item)??{}).year!=undefined?this.toThaiFormat((this.findReceipt(item)??{}).year,(this.findReceipt(item)??{}).month):"",
        // ((this.findReceipt(item)??{}).sequence!=undefined?"wma-":"") +((this.findReceipt(item)??{}).sequence??"-"),
        ((this.findReceipt(item)??{}).sequence??"-"),
        (((this.findReceipt(item)??{}).invoiceAmount??0)+((this.findReceipt(item)??{}).debtAmount??0)).formatFull(),
        ((this.findReceipt(item)??{}).paymentAmount??0).formatFull()
      ]);
    }); 
    workbook.xlsx.writeBuffer().then(function(data: any) {
      var blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });
      saveAs(blob, "ประวัติผู้ใช้น้ำ.xlsx");
    });
  }
}
