<div class="jm-input-container mb-3" :class="{'nomargin':nomargin}"><label class="input-title form-label">{{label}} {{min}}<span class="text-danger ms-1" v-if="required">*</span></label><input class="form-control" v-if="inputType=='number'" type="number" :value="modelValue" @input="emitup" :placeholder="placeholder==undefined?'กรุณากรอกข้อมูล':placeholder" :class="{'readonly':readonly}" :disabled="readonly" :required="required" :min="min"><input class="form-control" v-else-if="inputType=='secured'" type="password" :value="modelValue" @input="emitup" :placeholder="placeholder==undefined?'กรุณากรอกข้อมูล':placeholder" :class="{'readonly':readonly}" :disabled="readonly" :required="required"><input class="form-control" v-else :value="modelValue" @input="emitup" :placeholder="placeholder==undefined?'กรุณากรอกข้อมูล':placeholder" :class="{'readonly':readonly}" :disabled="readonly" :required="required"></div>