<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">เพิ่มข้อมูลการใช้น้ำด้วยไฟล์ Excel</h2><div class="row"><div class="col"><label class="mt-3">รหัสสายจัดเก็บ </label><label class="mt-3 text-danger">**กรุณาเลือกรหัสสายจัดเก็บแล้วจึงแนบไฟล์</label><select class="form-select form-select mb-3" v-model="code"><option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div></div><div class="row"><div class="col-6"><label class="mt-3">ข้อมูลประจำปี</label><select class="form-select form-select mb-3" v-model.number="year"> <option selected="">ปี</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6">   <label class="mt-3">เดือน</label><select class="form-select form-select mb-3" v-model.number="month"> <option selected="">เดือน</option><option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฏาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div></div><div class="row" v-if="this.activeContract != undefined"><label class="mb-0">Excel</label><div class="input-group mb-3"><input class="form-control" type="file" ref="file" label="Upload"><button class="btn btn-outline-secondary" type="button" @click="importExcel($event)">อัปโหลด</button></div></div><div class="row mb-3"><table class="table"><thead><tr class="table-dark text-center"><th>ลำดับ</th><th>เลขที่ผู้ใช้น้ำ</th><th>ชื่อ</th><th>ที่อยู่</th><th>ประเภท</th><th class="text-end">ปริมาณการใช้น้ำ (ลบ.ม.)</th><th class="text-end">อัตราค่าบริการ (บาท)</th></tr></thead><transition-group name="fade" tag="tbody"><tr class="text-center" v-for="doc,i in docs" :key="doc.number"><th scope="row">{{i+1}}</th><th>{{doc.meter}}</th><th class="text-start">{{doc.name}}</th><th>{{doc.address}}</th><th>{{doc.category}}</th><th class="text-end">{{(doc.qty)}}</th><th class="text-end">{{(doc.rate)}}</th></tr></transition-group></table></div><div class="mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/usage/list/`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="postData()">บันทึก</button></div></div>