<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4">บันทึกการรับเงินจาก {{(area.prefix??'')+(area.name??'')}}</h2><table class="table table-borderless tableFixHead"><thead><tr style="border-bottom:2px solid black;"><th>#</th><th>ประเภท</th><th>วันที่ชำระเงิน</th><th>จำนวนเงิน</th><th>หมายเหตุปี</th><th>หมายเหตุเดือน</th><th> <i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody><template v-for="(item,i) in docs"><tr><th>{{i+1}}  </th><td> <select class="form-select" v-model="item.category"><option>เช็ค</option><option>เงินสด</option><option>โอนเงิน</option><option>อื่นๆ</option></select></td><td><JMCalendar v-model="item.recordDate"></JMCalendar></td><td> <JMNumberLite v-model="item.amount" suffix="บาท"></JMNumberLite></td><td> <select class="form-select form-select-xs mb-3" v-model.number="item.year" @change="checkMonthYear"> <option selected="" :value="0">กรุณาเลือก</option><option>{{(new Date().getFullYear()+543)+1}}</option><template v-for="item,i in 10"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></td><td> <select class="form-select form-select-xs mb-3" aria-label=".form-select-lg example" v-model.number="item.month" @change="checkMonthYear"> <option selected="" :value="13">กรุณาเลือก</option><option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></td><td><a class="btn btn-danger" @click="deleteData(item._id)"><i class="fas fa-trash-alt"></i></a></td></tr><tr style="border-bottom:1px solid grey;"><td colspan="3"><JMUploads v-model="item.fileUrl" folder="collection"></JMUploads></td><td colspan="4"><ul class="list-group"><li class="list-group-item" v-for="(el,k) in item.fileUrl"> <div class="d-flex justify-content-between"><span>{{el}}</span><a :href="`${filePath}/${el}`"><i class="fas fa-cloud me-3"></i><span>ดาวน์โหลด</span></a><a class="btn btn-danger" @click="deleteUrl(item,k)"><i class="fas fa-trash-alt"></i></a></div></li></ul></td></tr></template></tbody></table><div class="d-flex justify-content-center mt-3 mb-5"><button class="btn btn-success text-white mx-2" type="button" @click="addItem()"><i class="fas fa-plus-circle me-2"></i><span>เพิ่มรายการ</span></button></div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/collection/list">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="updateItems()">บันทึก</button></div></form></div>