<div class="container-fluid"><div class="row"><div class="col-6 mb-3"><label class="form-label">QR Code / Bar Code</label><div class="form-check"><input class="form-check-input" id="showQR" type="checkbox" v-model="qrcode" @change="generatePDF('preview')"><label class="form-check-label" for="showQR">แสดง QRCode / Barcode</label></div></div><div class="col-6 mt-1"><JMCalendar v-model="invoiceDate" label="วันที่ออกใบแจ้งหนี้" @change="updateInvoiceDate()"></JMCalendar></div><div class="row"><canvas id="barcode" ref="barcode" style="opacity:0; display:none;"></canvas><div class="col-6"><JMUpload v-model="url" label="Logo"></JMUpload></div><div class="col-12 text-end m-2"><router-link class="btn btn-secondary mx-2" :to="`/portal/invoice/list?searchparam=${($route.query??{}).searchparam}`">เสร็จสิ้นการทำใบแจ้งหนี้</router-link></div></div><div class="row"><iframe :src="pdfBLOB" width="100%" style="height:100vh;"></iframe></div></div></div>