<div class="modal fade" id="signModal" tabindex="0" aria-labelledby="signModalLabel" aria-hidden="true"><div class="modal-dialog"><div class="modal-content"><div class="modal-body"><label class="form-label">คุณต้องการส่งคำร้องขอแก้ไข</label></div><div class="modal-footer"><button class="btn btn-light btn btn-secondary" type="button" data-bs-dismiss="modal">ยกเลิก</button><button class="btn btn-success text-white btn btn-secondary" type="button" data-bs-dismiss="modal" @click="sendRequest()">ยืนยัน</button></div></div></div></div><NoAccess v-if="store.state.access.findIndex(el =&gt; el == 'ใบนำส่ง/Invoice') ==-1"></NoAccess><div class="container-fluid p-4" v-else><StepIncome step="4"></StepIncome><h2 class="text-decoration-underline mb-4">รายการใบนำส่ง/Invoice</h2><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info mx-3" to="/portal/combine/form">จัดทำใบนำส่ง</router-link></div><JMIncomeStepSearch v-model="search" @clear="search={}; fetchData();" @search="fetchData();"></JMIncomeStepSearch><div class="row"><div class="col-6"><label>เลขที่เอกสาร</label><form class="d-flex"><input class="form-control form-control-lg me-2" type="search" placeholder="Search" aria-label="Search"><button class="btn btn-outline-success" type="submit">ค้นหา</button></form></div></div><div class="row mt-5"><label>จำนวนที่แสดง</label><div class="col-2 d-flex justify-content-start">  <select class="form-select form-select-sm mb-2" v-model.number="show" @change="fetchData()"><option selected="" value="9999999">ทั้งหมด</option><option value="50">50</option></select></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center align-middle"><th>ลำดับ</th><th>เลขที่เอกสาร/ชื่อเรื่อง</th><th>พื้นที่</th><th>ปีงบประมาณ</th><th>ไตรมาส</th><th>แก้ไขล่าสุด</th><th>เอกสารที่จัดทำแล้ว</th><th>...</th></tr></thead><tbody><tr class="text-center align-middle" v-for="item,i in docs"><th scope="row">{{i+1}} </th><td>{{item.documentNumber}} / {{item.subject}}</td><td>{{getAreaName(item.area)}}</td><td>{{item.calendarYear}}</td><td>{{item.quarter}}</td><td>{{(new Date(item.modifiedAt).toThaiLong())}}</td><td><ul class="list-group"> <li class="list-group-item" v-for="file in item.documents"><a :href="`${filePath}/${file}`"><i class="fas fa-cloud me-3"></i></a></li></ul></td><td><router-link class="btn btn-warning" :to="`/portal/combine/edit/${item._id}?searchparam=${JSON.stringify(this.search)}`" v-if="!item.isPrint">แก้ไข</router-link><router-link class="btn btn-warning" :to="`/portal/combine/edit/${item._id}`" v-else @click="doc=item;id=item._id;" type="button" data-bs-toggle="modal" data-bs-target="#signModal">ส่งคำร้อง</router-link><router-link class="btn btn-success" :to="`/portal/combine/edit/${item._id}`" target="_blank">พิมพ์</router-link></td></tr></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" tabindex="-1" aria-disabled="true">ก่อนหน้า</a></li><li class="page-item"><a class="page-link" href="#">1</a></li><li class="page-item"><a class="page-link" href="#">2</a></li><li class="page-item"><a class="page-link" href="#">3</a></li><li class="page-item"><a class="page-link" href="#">Next</a></li></ul></nav></div>