<div class="container-fluid"><div class="row"><div class="col-4"><JMUpload v-model="url" label="Logo"></JMUpload></div><div class="col-4"><label class="mb-2">ชื่อผู้รับเงิน</label><input class="form-control mb-3" type="text" v-model="name" @change="generatePDF('preview')"></div><div class="col-4"><label class="mb-2">ตำแหน่งผู้รับเงิน</label><input class="form-control mb-3" type="text" v-model="position" @change="generatePDF('preview')"></div><div class="col-6"><label class="mb-2">วิสัยทัศน์</label><textarea class="form-control mb-3" v-model="vision" @change="generatePDF('preview')"></textarea></div><div class="col-4 mb-3"><label class="form-label">เลือกพิมพ์ใบเสร็จรับเงิน/ใบกำกับภาษีส่วน</label><div class="form-check"><input class="form-check-input" id="print1" type="checkbox" v-model="type1" @change="generatePDF('preview')"><label class="form-check-label" for="stub">ต้นขั้ว</label></div><div class="form-check"><input class="form-check-input" id="fprint2" type="checkbox" v-model="type2" @change="generatePDF('preview')"><label class="form-check-label" for="copy"> สำเนา</label></div><div class="form-check"><input class="form-check-input" id="fprint2" type="checkbox" v-model="type3" @change="generatePDF('preview')"><label class="form-check-label" for="real"> ตัวจริง</label></div></div><div class="col-2 mb-3"><a class="btn btn-outline-success" @click="generatePDF('preview')">ปรับปรุงเอกสาร</a></div><iframe :src="pdfBLOB" width="100%" style="height:100vh;"></iframe></div></div>