<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4">จัดทำกรอบ 3</h2><div class="row justify-content-center"><div class="col-10 p-2 border border-4 border-light"><div class="mb-2 text-center">     <h5>ประมาณการสรุปรายรับ - จ่ายในการบริหารจัดการระบบบำบัดน้ำเสีย</h5><h5 class="ms-2 text-primary">{{(area.prefix??'')+(area.name??'')}}</h5></div><div class="mb-4 text-center">     <span>ประจำปีงบประมาณ </span><span class="text-primary me-1">{{year}} </span><span class="text-primary">(ไตรมาส {{quarter}})</span></div><table class="table"><thead><tr><th class="align-middle" colspan="2">ค่าใช้จ่ายในการดำเนินงานขององค์การจัดการน้ำเสีย</th><th class="text-end"><span class="me-4" v-if="isEndorsed">{{doc.oldText}}</span></th><th class="text-end" v-if="isEndorsed"><span class="me-4">{{doc.newText}}</span></th><th></th></tr></thead><tbody><template v-for="category,i in categories"><tr v-if="!showOnlyNonZeroes||((item.left??{}).totalAmount&gt;0||(item.right??{}).totalAmount&gt;0)"><th class="col-1 text-center">{{i+1}}             </th><th scope="row">{{category}}</th><th class="text-end" scope="row">{{((_.chain(union[i]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</th><th class="text-end" scope="row">{{((_.chain(union[i]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</th></tr></template><tr><th class="col-1 text-center"> </th><th scope="row">รวมค่าใช้จ่ายในการดำเนินงานขององค์การจัดการน้ำเสีย</th><th class="text-end border-bottom border-dark" scope="row"> <span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></th><th class="text-end border-bottom border-dark" scope="row"> <span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></th></tr><tr v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' "><th class="col-1 text-center"> </th><th scope="row"><div class="form-group row"><label class="col-sm-6 col-form-label" for="qtyYear">ปริมาณน้ำบำบัดได้ในปีงบประมาณ</label><div class="col-sm-3"><input class="form-control" id="qtyYear" type="number" v-model.number="doc.qtyYear"></div></div></th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.treatmentQty" suffix="ลบ.ม."></JMNumberLite></th><th class="text-end" scop="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eTreatmentQty" suffix="ลบ.ม."></JMNumberLite></th></tr><tr v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' "> <th class="col-1 text-center"> </th><th scope="row">ต้นทุนปันส่วนน้ำต่อหน่วย </th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.allocationPerUnit" suffix="บาท/ลบ.ม."></JMNumberLite></th><th class="text-end" scop="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eAllocationPerUnit" suffix="บาท/ลบ.ม."> </JMNumberLite></th></tr><tr v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' "><th class="col-1 text-center"> </th><th scope="row">ค่าใช้จ่ายปันส่วนตามปริมาณน้ำ</th><th class="text-end" scope="row"> <JMNumberLite v-model.number="doc.allocationCost" suffix="บาท" disabled></JMNumberLite></th><th class="text-end" scop="row" v-if="isEndorsed"><JMNumberLite v-model.number="doc.eAllocationCost" suffix="บาท" disabled></JMNumberLite></th></tr><tr v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' "><th class="col-1 text-center"> </th><th scope="row">รวมค่าใช้จ่ายทั้งสิ้นของ อจน.</th><th class="text-end" scope="row"> <JMNumberLite v-model.number="doc.wmaExpenses" suffix="บาท" disabled></JMNumberLite></th><th class="text-end" scop="row" v-if="isEndorsed"><JMNumberLite v-model.number="doc.eWmaExpenses" suffix="บาท" disabled></JMNumberLite></th></tr></tbody></table><div v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' &amp;&amp; this.ConditionCat!='สมทบตามจำนวนคงที่'"><table class="table"><thead><tr><th class="align-middle" colspan="2">ค่าใช้จ่ายในการดำเนินงานของ {{(area.prefix??'')+(area.name??'')}}</th><th></th><th></th></tr></thead><tbody><tr><th class="col-1 text-center"> </th><th scope="row">ค่าใช้จ่ายในการจัดเก็บค่าบริการ</th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.areaCost02" suffix="บาท"></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eAreaCost02" suffix="บาท"></JMNumberLite></th><th></th></tr><tr><th class="col-1 text-center"> </th><th scope="row">ค่าสื่อประชาสัมพันธ์</th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.areaCost03" suffix="บาท"></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"> <JMNumberLite @change="updateDoc" v-model.number="doc.eAreaCost03" suffix="บาท"></JMNumberLite></th><th></th></tr><template v-for="(item,index) in doc.expenses"><tr><th class="col-1 text-center"> </th><th scope="row"><input class="form-control" v-model="item.name"></th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="item.amount" suffix="บาท"></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="item.eAmount" suffix="บาท"></JMNumberLite></th><th><a class="btn btn-danger mx-2" @click="doc.expenses.splice(index,1)">    <i class="fas fa-trash-alt">        </i></a></th></tr></template><tr><th></th><td><a class="btn btn-success" @click="pushItem('expenses')">เพิ่มค่าใช้จ่าย</a></td><td></td><th></th><th></th></tr><tr><th class="col-1 text-center"> </th><th scope="row"> รวมค่าใช้จ่ายทั้งสิ้นของ {{(area.prefix??'')+(area.name??'')}}</th><th class="text-end" scope="row">{{(doc.areaExpenses??0).formatFull()}} บาท</th><th class="text-end" scope="row" v-if="isEndorsed">{{(doc.eAreaExpenses??0).formatFull()}} บาท</th><th></th></tr><tr><th class="col-1 text-center"> </th><th scope="row"> รวมค่าใช้จ่ายทั้งสิ้นของ อจน. และ {{(area.prefix??'')+(area.name??'')}}</th><th class="text-end" scope="row"> <span v-if="doc.areaExpenses == 0">- บาท</span><span v-else>{{(doc.totalExpenses??0).formatFull()}} บาท</span></th><th class="text-end" scope="row" v-if="isEndorsed">{{(doc.eTotalExpenses??0).formatFull()}} บาท</th><th></th></tr></tbody></table></div><div v-if="this.ConditionCat != 'สมทบตามเปอร์เซ็นต์' ">     <table class="table"><thead><tr><th class="align-middle" colspan="2">รายได้จากการจัดเก็บค่าบริการ(บำบัดน้ำเสีย,เชื่อมท่อ,ฯลฯ) ของ {{(area.prefix??'')+(area.name??'')}}</th><th></th><th></th></tr></thead><tbody><tr><th class="col-1 text-center"> </th><th scope="row"> รายได้จากการจัดเก็บค่าบริการ</th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.income01" suffix="บาท"></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eIncome01" suffix="บาท"></JMNumberLite></th></tr><tr><th class="col-1 text-center"> </th><th scope="row"> รายได้จากการต่อท่อเชื่อม</th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.income02" suffix="บาท"></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eIncome02" suffix="บาท"></JMNumberLite></th></tr><tr><th class="col-1 text-center"> </th><th scope="row">รวมรายได้จากการจัดเก็บค่าบริการ</th><th class="text-end" scope="row"> <JMNumberLite @change="updateDoc" v-model.number="doc.totalIncome" suffix="บาท" disabled></JMNumberLite></th><th class="text-end" scope="row" v-if="isEndorsed"><JMNumberLite @change="updateDoc" v-model.number="doc.eTotalIncome" suffix="บาท" disabled></JMNumberLite></th></tr></tbody></table></div></div><div class="d-flex justify-content-center mt-4"><router-link class="btn btn-secondary mx-2" :to="`/portal/calculation/list?searchparam=${$route.query.searchparam}`">ยกเลิก</router-link><button class="btn btn-success mx-2" type="button" @click="submitData()">บันทึก</button></div></div></div>