<div class="container-fluid p-4"> <h2 class="text-decoration-underline mb-5">รายการประวัติเลขที่ใบเสร็จ</h2><div class="row mt-2"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center"><th>ลำดับ</th><th>ประเภทเอกสาร</th><th>ปีงบประมาณ</th><th>ลำดับปัจจุบัน</th></tr></thead><tbody><tr class="text-center" v-for="(item,index) in docs"> <td>{{index+1}} </td><td>{{item.name.replace("Invoice","ใบแจ้งหนี้").replace("Usage","ทะเบียนคุมผู้ใช้บริการ").replace("Receipt","ใบเสร็จรับเงิน / ใบกำกับภาษี").replace("Payment","ผู้ชำระเงิน")}}</td><td>{{item.year}}</td><td>{{(item.sequence).formatComma()}}</td></tr></tbody></table></div></div></div>