<div class="container-fluid p-4"><form class="col-12" @submit.prevent="postData"><h2 class="text-decoration-underline mb-4">ข้อมูลใบเสร็จ</h2><div class="mt-5"></div><div class="mb-3"><label class="form-label">เลขที่ผู้ใช้น้ำ</label><input class="form-control" type="number" placeholder=" " v-model="doc.meter" disabled></div><div class="mb-3"><label class="form-label">ชื่อ - นามสกุล</label><input class="form-control" type="text" placeholder="" v-model="doc.name" disabled></div><div class="mb-3"><label class="form-label">เลขที่ใบเสร็จ</label><input class="form-control" type="number" placeholder="" v-model="doc.sequence" disabled></div><div class="mb-3"><label class="form-label">ประเภทลูกค้า </label><input class="form-control" type="number" placeholder="" v-model="doc.category" disabled></div><div class="mb-3"><label class="form-label">จำนวนเงินที่รับจริง </label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.paymentAmount" disabled><span class="input-group-text">บาท</span></div></div><p><span>{{doc.qty}} x {{doc.rate}} = {{doc.qty * doc.rate}}</span></p><p><span>{{doc.invoiceAmount}} </span></p><div class="mb-3"><label class="form-label">อัตราภาษี</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model.number="doc.vatRate" @input="updateInvoiceAmount()"><span class="input-group-text">บาท</span></div></div><div class="mb-3"><label class="form-label">จำนวนเงินตามใบแจ้งหนี้</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model.number="doc.invoiceAmount"><span class="input-group-text">บาท</span></div></div><div class="mb-3"><label class="form-label">ยอดเงินคงค้าง</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.debtAmount" disabled><span class="input-group-text">บาท</span></div></div><div class="row mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/receipt/list?searchparam=${($route.query??{}).searchparam}`">ยกเลิก</router-link><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()">ลบข้อมูล</a></div></form></div>