<div class="container-fluid p-4"><h2 class="text-decoration-underline">ประวัติผู้ใช้น้ำ</h2><div class="col"><label class="text-muted">(รายงานสรุปลูกหนี้รายบุคคล)</label></div><div class="row"><div class="col-6"><label class="form-label">สถานะการชำระ:</label><select class="form-select form-select-xs mb-3" v-model="search.isPaid"><option selected="">ทั้งหมด</option><option :value="true">ชำระแล้ว</option><option :value="false">ยังไม่ได้ชำระ</option></select></div><div class="col-6"><label class="form-label">ค้นหาใบแจ้งหนี้</label><input class="form-control" v-model="search.text"></div></div><div class="row"><div class="col-6"><label class="mt-3">ข้อมูลประจำปี:</label><select class="form-select form-select-xs mb-3" v-model="search.year"><option selected="" :value="0">กรุณาเลือก</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6">   <label class="mt-3">ข้อมูลปริมาณน้ำประจำเดือน</label><select class="form-select form-select-xs mb-3" v-model="search.month"><option selected="">เดือน</option><option value="1">มกราคม</option><option value="2">กุมภาพันธ์</option><option value="3">มีนาคม</option><option value="4">เมษายน</option><option value="5">พฤษภาคม</option><option value="6">มิถุนายน</option><option value="7">กรกฎาคม</option><option value="8">สิงหาคม</option><option value="9">กันยายน</option><option value="10">ตุลาคม</option><option value="11">พฤศจิกายน</option><option value="12">ธันวาคม</option></select></div><div class="d-flex justify-content-end"><button class="btn btn-outline-success mx-2" type="submit" @click="search={}; fetchData()">ล้างการค้นหา</button></div></div><div class="col mt-5"><label class="mb-3">เลขที่ผู้ใช้น้ำ {{$route.params.id}}</label><label class="mb-3 px-2">{{(docs[0]??{}).name}} </label></div><div class="col d-flex justify-content-end align-items-center mb-2"><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel </span></button></div><div class="row mt-2"><div class="col"><span>จำนวนใบแจ้งหนี้</span><span class="text-primary mx-2">{{(docs.length??0).formatComma()}} </span><span>ใบ</span><span class="ms-3"> |</span><span class="ms-4">ยอดค้างชำระล่าสุด</span><span class="text-primary mx-2">{{((docs[docs.length-1]??{debtAmount:0}).debtAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนใบเสร็จรับเงิน</span><span class="text-primary mx-2">{{(receipts.length??0).formatComma()}}</span><span>ใบ</span><span class="ms-3"> |</span><span class="ms-4">จำนวนค่าบริการ</span><span class="text-primary mx-2">{{(docs.map(el=>el.totalAmount).reduce((a,b)=>a+b,0)??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">ภาษีมูลค่าเพิ่ม</span><span class="text-primary mx-2">{{((docs.map(el=>el.vat).reduce((a,b)=>a+b,0)??0)).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">รวมต้องชำระทั้งสิ้น</span><span class="text-primary mx-2">{{((docs.map(el=>el.totalAmount+el.vat).reduce((a,b)=>a+b,0)??0)).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">ชำระค่าบริการแล้วรวมทั้งสิ้น</span><span class="text-primary mx-2">{{(receipts.map(el=>el.paymentAmount).reduce((a,b)=>a+b,0)??0).formatFull()}}</span><span>บาท</span></div></div><table class="table table-striped"><thead class="table-dark"><tr class="text-center"><th scope="col" colspan="9">ค้างชำระ</th><th scope="col" colspan="4" style="border-left:1px solid white;">ชำระจริง</th></tr><tr class="text-center"><th>ลำดับ</th><th>หมายเลขใบแจ้งหนี้</th><th>ประจำเดือน</th><th>ปริมาณการใช้น้ำ (ลบ.ม.)</th><th>อัตราค่าบำบัด</th><th>รวมเงิน</th><th>ภาษี (บาท)</th><th>ค่าบริการบำบัดน้ำเสีย</th><th style="border-left:1px solid white;">เดือนชำระ   </th><th>หมายเลขใบเสร็จ</th><th>ชำระทั้งสิ้น</th><th>จำนวนเงินที่รับจริง</th></tr></thead><tbody class="text-center"><template v-for="item,i in filtered"><tr><td scope="row">{{i+1}}</td><td>{{item.sequence}}</td><td> <span>{{toThaiFormat(item.year,item.month)}}</span></td><td class="text-end"> <span>{{(item.qty??0).formatFull()}}</span></td><td class="text-end"> <span>{{(item.rate??0).formatFull()}}</span></td><td class="text-end"><span>{{(item.totalAmount??0).formatFull()}}</span></td><td class="text-end"> <span>{{(item.vat??0).formatFull()}}</span></td><td class="text-end"><span>{{((item.totalAmount??0)+(item.vat??0)).formatFull()}}</span></td><td style="border-left:1px solid #AAA;"> <template v-if="findReceipt(item)"><span>{{toThaiFormat((findReceipt(item)??{}).year,(findReceipt(item)??{}).month)}}</span></template><template v-else><span>-  </span></template></td><td><span>{{((findReceipt(item)??{}).sequence??"-")}}</span></td><td> <span>{{(((findReceipt(item)??{}).invoiceAmount??0)+((findReceipt(item)??{}).debtAmount??0)).formatFull()}}</span></td><td> <span>{{((findReceipt(item)??{}).paymentAmount??0).formatFull()}}</span></td></tr></template></tbody></table></div>