<div class="modal fade" id="scoreModal" tabindex="0" aria-labelledby="compareModalLabel" aria-hidden="true"><div class="modal-dialog modal-lg"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="exampleModalLabel">แบบฟอร์มการรับคำร้อง</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><form><div class="mb-3"><label class="form-label">ชื่อผู้ร้องเรียน</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.name"></div></div><div class="mb-3"><JMCalendar label="วันที่" v-model="doc.date"></JMCalendar></div><div class="col-12 mb-3"><div class="row"><div class="col-6"><label class="form-label">เลขที่ผู้ใช้น้ำ</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.meter"></div></div><div class="col-6"><label class="form-label">เลขที่ใบแจ้งหนี้</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.invoiceNumber"></div></div></div></div><h6 class="form-label">ที่อยู่ :</h6><div class="col-12 mb-3"><div class="row"><div class="col-4"><label class="form-label">บ้านเลขที่</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.address"></div></div><div class="col-4"><label class="form-label">หมู่</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.moo"></div></div><div class="col-4"><label class="form-label">ซอย</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.soi"></div></div></div><div class="row"><div class="col-4"><label class="form-label">ถนน</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.street"></div></div><div class="col-4"><label class="form-label">ตำบล</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.subDistrict"></div></div><div class="col-4"><label class="form-label">อำเภอ</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.district"></div></div></div><div class="row"><div class="col-6"><label class="form-label">จังหวัด</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.province"></div></div><div class="col-6"><label class="form-label">โทร</label><div class="input-group mb-3"><input class="form-control" type="text" placeholder="" v-model="doc.tel"></div></div></div></div><div class="col-12 mb-3"><label class="form-label">ประเภทการร้องเรียน</label><div class="row ms-3"> <div class="col-6 form-check"><input class="form-check-input" id="complaint1" type="checkbox" name="complaint" value="ไม่มีท่อเทศบาลผ่าน" v-model="doc.complaint"><label class="form-check-label" for="complaint1">ไม่มีท่อเทศบาลผ่าน</label></div><div class="col-6 form-check"><input class="form-check-input" id="complaint2" type="checkbox" name="complaint" value="มีท่อผ่าน แต่ไม่มีการเชื่อมท่อ" v-model="doc.complaint"><label class="form-check-label" for="complaint2">มีท่อผ่าน แต่ไม่มีการเชื่อมท่อ</label></div><div class="col-6 form-check"><input class="form-check-input" id="complaint3" type="checkbox" name="complaint" value="มีระบบบำบัดน้ำเสียของตนเอง" v-model="doc.complaint"><label class="form-check-label" for="complaint3">มีระบบบำบัดน้ำเสียของตนเอง</label></div><div class="col-6 form-check"><input class="form-check-input" id="complaint4" type="checkbox" name="complaint" value="ประเภทกิจการไม่ถูกต้อง" v-model="doc.complaint"><label class="form-check-label" for="complaint4">ประเภทกิจการไม่ถูกต้อง</label></div><div class="col-6 form-check"><input class="form-check-input" id="complaint5" type="checkbox" name="complaint" value="ชื่อที่อยู่ไม่ถูกต้อง" v-model="doc.complaint"><label class="form-check-label" for="complaint5">ชื่อที่อยู่ไม่ถูกต้อง</label></div><div class="col-6 form-check"><input class="form-check-input" id="complaint6" type="checkbox" name="complaint" value="อื่นๆ" v-model="doc.complaint"><label class="form-check-label" for="complaint6">อื่นๆ</label></div></div></div><div class="col-12 mb-3"><label class="form-label">ช่องทางการชำระเงิน</label><div class="row ms-3"> <div class="col-6 form-check"><input class="form-check-input" id="payment1" type="checkbox" name="payment" value="ชำระที่สำนักงานจัดการน้ำเสีย" v-model="doc.payment"><label class="form-check-label" for="payment1">ชำระที่สำนักงานจัดการน้ำเสีย</label></div><div class="col-6 form-check"><input class="form-check-input" id="payment2" type="checkbox" name="payment" value="ชำระผ่านบัญชีธนาคาร" v-model="doc.payment"><label class="form-check-label" for="payment2">ชำระผ่านบัญชีธนาคาร</label></div><div class="col-6 form-check"><input class="form-check-input" id="payment3" type="checkbox" name="payment" value="ชำระผ่านพนักงานจัดเก็บ" v-model="doc.payment"><label class="form-check-label" for="payment3">ชำระผ่านพนักงานจัดเก็บ</label></div></div></div><div class="mb-3"><label class="form-label">เรื่องที่ต้องการแก้ไข</label><div class="form-group"><textarea class="form-control" id="exampleFormControlTextarea1" rows="/" placeholder="กรุณากรอกข้อมูล" v-model="doc.complaintDetail">     </textarea></div></div><div class="mb-3"><label class="form-label">ข้อคิดเห็นเสนอแนะ</label><div class="form-group"><textarea class="form-control" id="exampleFormControlTextarea2" rows="/" placeholder="กรุณากรอกข้อมูล" v-model="doc.note"> </textarea></div></div><div class="mb-3"><div class="row"><label class="form-label">แนบใบคำร้อง</label><JMUploads v-model="doc.fileUrl"></JMUploads></div></div><div class="modal-footer justify-content-center"><div class="d-flex"><button class="btn btn-secondary mx-2" type="button" @click="$router.go(-1)">ยกเลิก</button><button class="btn btn-success mx-2" type="button" @click="submit()">ยืนยัน</button></div></div></form></div></div></div></div><div class="container"><h3 class="mt-3">ตรวจสอบยอดค้างชำระล่าสุด</h3><div class="d-flex justify-content-end mb-3"> <a class="btn btn-warning mx-3" type="button" data-bs-toggle="modal" data-bs-target="#scoreModal">ยื่นใบคำร้อง</a></div><div class="row"><div class="col-12"><div class="card h-100 bg-info"><div class="card-body row"><table class="table table-borderless" v-for="(item,i) in docs"><tbody><tr><td class="fw-bolder">เลขที่ผู้ใช้น้ำ</td><td class="fw-light">{{item.meter}} </td><td class="fw-bolder"> ชื่อ</td><td class="fw-light">{{item.name}}</td></tr><tr><td class="fw-bolder"> ประเภทผู้ใช้น้ำ</td><td class="fw-light">{{item.category}}</td><td class="fw-bolder"> ที่อยู่</td><td class="fw-light">{{item.address}}</td></tr><tr><td class="fw-bolder"> วันที่ออกใบแจ้งหนี้</td><td class="fw-light">{{item.createdAt == null ? "" : new Date(item.createdAt).toThaiShort()}}  </td></tr><tr><td class="fw-bolder"> เลขที่ใบแจ้งหนี้</td><td class="fw-light">{{item.sequence}}</td></tr></tbody></table></div></div></div><div class="col-12 h-100 mt-3"><div class="card h-100 border-primary justify-content-center"><div><table class="table table-borderless second"><tbody v-for="(item,i) in docs"><tr><td class="fw-bolder">ยอดค้างชำระตั้งแต่</td><td class="text-end fw-light" colspan="8">{{item.debtText}}</td><td class="text-end fw-light">{{(item.debtAmount??0).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr><tr><td class="fw-bolder">บริการใช้น้ำครั้งนี้</td><td class="text-end fw-light" colspan="8">{{getThaiMonth(item.month)}}{{(item.year??0).toString().substring(2,4)}}</td><td class="text-end fw-light">{{(item.qty??0).formatFull()}}</td><td class="text-end fw-light">ลบ.ม.</td></tr><tr><td class="fw-bolder">ผู้บำบัดน้ำเสียตามประเภทผู้ใช้น้ำ (บาท/ลบ.ม.)</td><td class="text-end fw-light" colspan="9">{{(item.rate??0).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr><tr><td class="fw-bolder">รวมเงิน</td><td class="text-end fw-light" colspan="9">{{(item.totalAmount??0).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr><tr><td class="fw-bolder">ภาษีมูลค่าเพิ่ม</td><td class="text-end fw-light" colspan="9">{{(item.vat??0).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr><tr><td class="fw-bolder">ค่าบริการบำบัดน้ำเสียที่ต้องชำระ</td><td class="text-end fw-light" colspan="9">{{((item.totalAmount??0)+(item.vat??0)).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr><tr><td class="fw-bolder">รวมที่ต้องชำระทั้งสิ้น</td><td class="text-end fw-light" colspan="9">{{(((item.totalAmount??0)+(item.vat??0))+(item.debtAmount??0)).formatFull()}}</td><td class="text-end fw-light">บาท</td></tr></tbody></table></div></div></div><h3 class="mt-4">ช่องทางชำระเงิน</h3><div class="col-12 h-100 mt-3 mb-5"><div class="card h-100 bg-primary justify-content-center"><div><div class="card-body"><p class="card-text"> 1. สแกน QR Code ในแอปพลิเคชันสำหรับชำระเงิน</p><p class="card-text"> 2. ชำระเงินผ่านทางธนาคาร</p></div></div></div></div></div></div>