<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='area')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">รายการพื้นที่องค์การปกครองส่วนท้องถิ่น (อปท.)</h2><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info mx-3" to="/portal/area/form">เพิ่มอปท.</router-link></div><JMIncomeSearch class="mb-5" @search="fetchData()" v-model="search"></JMIncomeSearch><div class="row"><JMPaginateSelect v-model.number="show" @change="fetchData()"></JMPaginateSelect><div class="col-12 p-2"><table class="table table-striped table-hover"><thead class="table-dark"><tr class="text-center"><th class="align-middle" scope="col" rowspan="2">ลำดับ</th><th class="align-middle" scope="col" rowspan="2">เลขที่สัญญา</th><th class="align-middle" scope="col" rowspan="2">รหัสพื้นที่</th><th class="align-middle" scope="col" rowspan="2">พื้นที่</th><th class="align-middle" scope="col" rowspan="2">วันที่เริ่มสัญญา</th><th class="align-middle" scope="col" rowspan="2">วันที่หมดสัญญา</th><th class="align-middle" scope="col" rowspan="2">จำนวนปีตามสัญญา</th><th class="align-middle" scope="col" rowspan="2">เอกสารสัญญา</th><th class="align-middle" scope="col" rowspan="2"><i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody><tr class="text-center" v-for="(item,i) in docs"><th scope="row">{{(page*show)+(i+1)}}</th><td>{{item.contractNumber}}</td><td>{{item.code}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{convertToThaiDate(item.contractStart)}}</td><td>{{convertToThaiDate(item.contractEnd)}}</td><td>{{item.contractYear}}</td><td><ul class="list-group"><li class="list-group-item" v-for="file in item.fileUrl1"><a :href="`${filePath}/${file}`"><i class="fas fa-cloud me-3"></i><span>{{file}}</span></a></li><li class="list-group-item" v-for="file in item.fileUrl2"><a :href="`${filePath}/${file}`"><i class="fas fa-cloud me-3"></i><span>{{file}}</span></a></li></ul></td><td class="text-center"><router-link class="btn btn-warning" :to="`/portal/area/edit/${item._id}`"><span>แก้ไข</span></router-link></td></tr></tbody></table></div></div><nav><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)">ก่อนหน้า</a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)">ถัดไป</a></li></ul></nav></div>