<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='ทะเบียนคุมใบแจ้งหนี้')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">ทะเบียนคุมใบแจ้งหนี้</h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span>ดาวน์โหลด Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row d-print-none"><div class="col-3"><label class="mt-3">พื้นที่</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected="">กรุณาเลือก</option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-3"> <label class="mt-3">ประเภท</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected="">ประเภท</option><option value="1,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 1</option><option value="2,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 2</option><option value="3,น้ำเสีย">ค่าบำบัดน้ำเสียประเภทที่ 3</option><option value="1,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 1</option><option value="2,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 2</option><option value="3,น้ำทิ้ง">ค่าบำบัดน้ำทิ้งประเภทที่ 3</option><option value="4,เหมา">ค่าบำบัดน้ำเสียประเภทเหมาจ่าย</option></select></div><div class="col-3">  <label class="mt-3">เดือน</label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.month"> <option :value="1">มกราคม</option><option :value="2">กุมภาพันธ์</option><option :value="3">มีนาคม</option><option :value="4">เมษายน</option><option :value="5">พฤษภาคม</option><option :value="6">มิถุนายน</option><option :value="7">กรกฎาคม</option><option :value="8">สิงหาคม</option><option :value="9">กันยายน</option><option :value="10">ตุลาคม</option><option :value="11">พฤศจิกายน</option><option :value="12">ธันวาคม</option></select></div><div class="col-3"><label class="mt-3">ปี</label><select class="form-select form-select-xs mb-3" v-model.number="search.year"> <option selected="" :value="0">กรุณาเลือก</option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div></div><div class="mt-5 mb-5 table-print">     <h5 class="text-center">ทะเบียนคุมใบแจ้งหนี้ <span class="text-primary">{{(rates.find(el=>el.code==search.code)??{}).name}}</span><span>ประเภท  </span><span class="text-primary">{{search.cat}}</span><span>ตามปริมาณน้ำประปา ประจำเดือน </span><span class="text-primary">{{getThaiMonth(search.month)}} {{search.year}}</span></h5></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span>กำลังดึงข้อมูลจากแม่ข่าย</span></div></transition></div></div><div class="row mt-2 table-print"><div class="col"><span>จำนวนผู้ใช้น้ำ </span><span class="text-primary" id="totalCount">{{(totalCount??0).formatComma()}} </span><span> คน</span><span class="ms-3"> |</span><span class="ms-4">ปริมาณการใช้น้ำ</span><span class="text-primary mx-2">{{(totalQty??0).formatFull()}}</span><span>ลบ.ม.</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินที่ต้องเรียกเก็บ </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-3"> |</span><span class="ms-4">จำนวนเงินที่ต้องเรียกเก็บ + ยอดค้างชำระ</span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span>บาท</span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle" scope="col" rowspan="2">ลำดับ</th><th class="align-middle" scope="col" rowspan="2">เลขที่ใบแจ้งหนี้</th><th class="align-middle" scope="col" colspan="1">เลขที่ผู้ใช้น้ำ (ใหม่)</th><th class="align-middle" scope="col" rowspan="2">ชื่อ</th><th class="align-middle" scope="col" rowspan="2">ที่อยู่</th><th class="align-middle" scope="col" colspan="1">เดือนที่ค้างชำระ</th><th class="align-middle text-end" scope="col" rowspan="2">เงินคงค้าง (บาท)</th><th class="align-middle" scope="col" colspan="1">ปริมาณการใช้น้ำ</th><th class="align-middle" scope="col" colspan="1">ค่าบำบัดน้ำเสีย</th><th class="align-middle text-end" scope="col" rowspan="2">รวมเงิน (บาท)</th><th class="align-middle text-end" scope="col" rowspan="2">ภาษี</th><th class="align-middle" scope="col" colspan="1">ค่าบริการบำบัดน้ำเสีย</th><th class="align-middle text-end" scope="col" rowspan="2">รวมเงินชำระทั้งสิ้น (บาท)</th></tr><tr class="text-center"><th>ปรับปรุงเมื่อ </th><th>(เดือน)</th><th>(ลบ.ม.)</th><th>ประเภท</th><th>ที่ต้องชำระ</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}  </th><td>{{item.sequence}}</td><td class="text-start">{{item.meter}}</td><td class="text-start">{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-start">{{item.address}}</td><td>{{(item.debtText??"").length==0?"-":item.debtText}}</td><td class="text-end">{{(item.debtAmount??0).formatFull()}}</td><td class="text-end">{{(item.qty??0).formatFull()}}</td><td>{{item.category}} </td><td class="text-end">{{sum(item).formatFull()}} </td><td class="text-end">{{(sum(item)*(item.vatRate)).formatFull()}}  </td><td class="text-end">{{(sum(item)*item.vat+(item.rate*item.qty)).formatFull()}} </td><td class="text-end">{{((sum(item)*item.vat+(item.rate*item.qty))+(item.debtAmount??0)).formatFull()}} </td></tr></tbody></table></div></div></div>