<div class="container"><h2 class="mt-5">เพิ่มไฟล์ข้อมูล สตง.</h2><div class="row mb-3"><div class="col-6"><label class="mt-3">ปีงบประมาณ</label><select class="form-select form-select mb-3" v-model.number="year" @change="postData"> <option selected="">ปี</option><template v-for="item,i in 16"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span class="me-3">{{importStatus}}</span></div></transition></div><div class="col-6"><label class="mt-3">อัปโหลดไฟล์</label><input class="form-control" type="file" label="Upload" @input="importExcel($event)"></div></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="submitData()">อัปโหลด</button></div><div class="row"><table class="table table-bordered table-print"><thead class="text-center bg-info"><tr><th rowspan="2">ลำดับ</th><th rowspan="2">รายชื่อพื้นที่</th><th rowspan="2">รหัสพื้นที่</th><th rowspan="2">ประเภทการจัดเก็บ</th><th colspan="4">ไตรมาส 1</th><th colspan="4">ไตรมาส 2</th><th colspan="4">ไตรมาส 3</th><th colspan="4">ไตรมาส 4</th><th rowspan="2">รวม</th></tr><tr><th>ต.ค.</th><th>พ.ย.</th><th>ธ.ค.</th><th>รวม</th><th>ม.ค.</th><th>ก.พ.</th><th>มี.ค.</th><th>รวม</th><th>เม.ย.</th><th>พ.ค.</th><th>มิ.ย.</th><th>รวม</th><th>ก.ค.</th><th>ส.ค.</th><th>ก.ย.</th><th>รวม</th></tr></thead><tbody><template v-for="doc,i in conditions"><tr class="text-end"><td>{{i+1}}</td><td class="text-start">{{doc.name}}</td><td class="text-start">{{doc.code}}</td><td>{{doc.period}}</td><template v-if="doc.months!=undefined"><td class="t">{{(doc.months[0]??0).formatFull()}}</td><td class="t">{{(doc.months[1]??0).formatFull()}}</td><td class="t">{{(doc.months[2]??0).formatFull()}}</td><td class="t">{{((doc.months[0]??0)+(doc.months[1]??0)+(doc.months[2]??0)).formatFull()}}</td><td class="t">{{(doc.months[3]??0).formatFull()}}</td><td class="t">{{(doc.months[4]??0).formatFull()}}</td><td class="t">{{(doc.months[5]??0).formatFull()}}</td><td class="t">{{((doc.months[3]??0)+(doc.months[4]??0)+(doc.months[5]??0)).formatFull()}}</td><td class="t">{{(doc.months[6]??0).formatFull()}}</td><td class="t">{{(doc.months[7]??0).formatFull()}}</td><td class="t">{{(doc.months[8]??0).formatFull()}}</td><td class="t">{{((doc.months[6]??0)+(doc.months[7]??0)+(doc.months[8]??0)).formatFull()}}</td><td class="t">{{(doc.months[9]??0).formatFull()}}</td><td class="t">{{(doc.months[10]??0).formatFull()}}</td><td class="t">{{(doc.months[11]??0).formatFull()}}</td><td class="t">{{((doc.months[9]??0)+(doc.months[10]??0)+(doc.months[11]??0)).formatFull()}}</td><td class="t">{{((doc.months[0]??0)+(doc.months[1]??0)+(doc.months[2]??0)+(doc.months[3]??0)+(doc.months[4]??0)+(doc.months[5]??0)+(doc.months[6]??0)+(doc.months[7]??0)+(doc.months[8]??0)+(doc.months[9]??0)+(doc.months[10]??0)+(doc.months[11]??0)).formatFull()}}</td></template></tr></template></tbody></table></div></div>