<div class="container-fluid p-4"><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()">พิมพ์</button></div><div class="row mb-2"><div class="col"><span>จำนวนผู้ใช้น้ำ </span><span class="text-primary mx-2">{{(totalCount??0).formatComma()}} </span><span>คน</span><span class="ms-1"> |</span><span class="ms-1">จำนวนเงินที่รับจริง</span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">จำนวนเงินตามใบแจ้งหนี้ </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">รวมภาษีขาย </span><span class="text-primary mx-2">{{receipts.map(el=>el.vat).reduce((a,b)=>a+b,0).formatFull()}}</span><span>บาท</span><span class="ms-1"> |</span><span class="ms-1">ยอดเงินคงค้าง</span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span>บาท</span></div></div><div class="row"><table class="table small-text-table"><thead><tr><th>ลำดับ</th><td>เลขที่ใบแจ้งหนี้</td><td>เลขที่ผู้ใช้น้ำ</td><td>เลขที่ใบเสร็จรับเงิน</td><td>ประเภทผู้ใช้</td><td>ชื่อ-นามสกุล</td><td>ที่อยู่</td><td>ยอดเงินคงค้าง</td><td>ค่าบริการก่อนหักภาษี</td><td>ภาษีมูลค่าเพิ่ม</td><td>ยอดเงินปัจจุบัน</td><td>จำนวนเงินตามใบแจ้งหนี้</td><td>จำนวนเงินที่รับจริง</td></tr></thead><tbody> <tr v-for="item,i in receipts"><th>{{i+1}}</th><td>{{item.invoiceNumber}}</td><td>{{item.meter}}</td><td>WMA-{{item.sequence}}</td><td>{{item.category}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.address}}</td><td>{{(item.debtAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{((item.billAmount*1.07)||0).formatFull()}}</td><td>{{(item.invoiceAmount??0).formatFull()}} </td><td>{{(item.invoiceAmount??0).formatFull()}} </td><td>{{(item.paymentAmount??0).formatFull()}}</td></tr></tbody></table></div><h5 class="strong">เรียน ผอ. ฝบจ.</h5><p>กวพ.ได้จัดทำใบเสร็จรับเงินค่าบริการบำบัดน้ำเสีย <span>| ผู้ใช้น้ำประเภท 1</span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='1')??[]).length}} ใบ</span><span>| ผู้ใช้น้ำประเภท 2 </span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='2')??[]).length}} ใบ</span><span>| ผู้ใช้น้ำประเภท 3</span><span class="ms-2">จำนวน</span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='3')??[]).length}} ใบ</span><span>ประจำวันที่ ...</span><span>เป็นจำนวนเงินตามใบเสร็จ </span><span class="mx-3 text-primary">{{(totalAmount??0).formatFull()}}</span><span>บาท (ดังแนบ)</span></p><p class="ms-5">จึงเรียนมาเพื่อโปรดพิจารณา</p></div>